import React, { useState, useEffect, useRef } from "react";
import classNames from "classnames";
import { DataTable } from "primereact/datatable";
import { Dropdown } from "primereact/dropdown";
import { Column } from "primereact/column";
import { Toast } from "primereact/toast";
import { Button } from "primereact/button";
import { Toolbar } from "primereact/toolbar";
import { Dialog } from "primereact/dialog";
import { InputText } from "primereact/inputtext";
import StudentService from "../service/StudentService";
import { filterSiswa } from "../utils/formatDate";
import SekolahService from "../service/SekolahService";
import { ClassesService } from "../service/ClassesService";

export const Students = () => {
    const sekolahService = new SekolahService();
    const classesService = new ClassesService();
    const studentService = new StudentService();
    const emptyStudent = {
        id: null,
        nisn: null,
        nis: null,
        nama: "",
        kotaLahir: "",
        tanggalLahir: null,
    };

    const sekolahEmpty = {
        _id: "",
        namaSekolah: "",
        email: "",
        telepon: "",
        alamatSekolah: "",
        logo: "",
    };

    const classesEmpty = [
        {
            id: "",
            name: "",
        },
    ];

    const selctedEmpty = [];

    const [student, setStudent] = useState(emptyStudent);
    const [students, setStudents] = useState(null);
    const [studentDialog, setStudentDialog] = useState(false);
    const [deleteKelasStudentsDialog, setDeleteKelasStudentsDialog] = useState(false);
    const [selectedStudents, setSelectedStudents] = useState(selctedEmpty);
    const [kelasSekarang, setKelasSekarang] = useState(null);

    const [kelas, setKelas] = useState(null);
    const [kelasDropdown, setKelasDropdown] = useState(classesEmpty);
    const [nameDialog, setNameDialog] = useState("");
    const [nameSelectedDialog, setNameSelectedDialog] = useState("");
    const [tahunPelajaran, setTahunPelajaran] = useState("");
    const [hapusKelasDialog, setHapusKelasDialog] = useState(false);
    const [selectedDialog, setSelectedDialog] = useState(false);

    const [submitted, setSubmitted] = useState(false);
    const [globalFilter, setGlobalFilter] = useState(null);
    const toast = useRef(null);
    const dt = useRef(null);

    useEffect(() => {
        const fetchData = async () => {
            const getDataClasses = await classesService.getAllClasses();
            setKelasDropdown(getDataClasses.data.data);
        };
        fetchData();
    }, []);

    useEffect(() => {
        const fetchStudents = async () => {
            const getStudents = await studentService.getStudents();

            const getTpSekolah = await sekolahService.getTpSekolah();
            const tp = getTpSekolah.data.data.tahunPelajaranSekarang;
            setTahunPelajaran(tp);

            //Menampilkan semua data siswa dengan cara melihat tahun pelajaran (yang ada kelas otomatis tampil dan hanya tersedia button pindah)
            const filterStudents = filterSiswa(getStudents.data.data, tp);
            setStudents(filterStudents);
        };
        fetchStudents();
    }, [studentDialog, hapusKelasDialog, student]);

    const getNameClasses = (id) => {
        const kelas = kelasDropdown.filter((item) => {
            return item.id === id;
        });
        if (kelas.length) return kelas[0].name;
    };

    const selectedInfo = selectedStudents.map((x) => {
        if (x.kelas.idKelas) {
            return false;
        }
        return true;
    });

    const selectedTambah = selectedInfo.filter((x) => {
        return x === false;
    });
    const selectedPindah = selectedInfo.filter((x) => {
        return x === true;
    });

    const hideStudent = () => {
        setSubmitted(false);
        setStudentDialog(false);
        setKelas(null);
        setKelasSekarang(null);
    };

    const hideDeleteKelasStudentsDialog = () => {
        setDeleteKelasStudentsDialog(false);
    };

    const tambahKelas = (student) => {
        setStudent({ ...student });
        setStudentDialog(true);
        setNameDialog("Tambah Kelas");
    };

    const pindahKelas = (student) => {
        setStudent({ ...student });
        setStudentDialog(true);
        setNameDialog("Pindah Kelas");
        //insialisasi kelas yang sudah ada
        const setPindahKelas = kelasDropdown.filter((x) => x.id === student.kelas.idKelas);
        setKelasSekarang(setPindahKelas[0]);
        setKelas(setPindahKelas[0]);
    };

    const hapusKelas = () => {
        setSubmitted(true);
        if (kelas) {
            studentService
                .deleteKelasStudent({ id: student.id, idKelas: kelas.id, tahunPelajaran })
                .then((res) => {
                    setStudent(emptyStudent);
                    setSelectedStudents(selctedEmpty);
                    setKelas(null);
                    setKelasSekarang(null);
                    hideHapusKelasDialog();
                    toast.current.show({ severity: "success", summary: "Successful", detail: res.data.message, life: 3000 });
                })
                .catch((error) => {
                    const errResponse = error.response;
                    if (!errResponse.data.message) {
                        toast.current.show({ severity: "error", summary: "Internal Server Error", detail: error.response.statusText, life: 3000 });
                    } else {
                        toast.current.show({ severity: "warn", summary: "Warning", detail: error.response.data.message, life: 3000 });
                    }
                });
        }
    };
    const bukaHapusKelas = (student) => {
        setStudent({ ...student });
        setHapusKelasDialog(true);

        //insialisasi kelas yang sudah ada
        const setPindahKelas = kelasDropdown.filter((x) => x.id === student.kelas.idKelas);
        setKelas(setPindahKelas[0]);
    };

    const hideHapusKelasDialog = () => {
        setHapusKelasDialog(false);
    };

    const hideSelectedDialog = () => {
        setSelectedDialog(false);
    };

    const simpanSelectedTambahKelas = () => {
        const tambahArray = selectedStudents.map((x) => {
            return { id: x.id, idKelas: kelas.id, tahunPelajaran };
        });
        setSubmitted(true);
        if (kelas) {
            studentService
                .postKelasStudents(tambahArray)
                .then((res) => {
                    setStudent(emptyStudent);
                    setKelasSekarang(null);
                    setSelectedStudents([]);
                    hideSelectedDialog();
                    let offset = 0;
                    return res.data.message.map((x, i) => {
                        setTimeout(() => {
                            toast.current.show({ severity: x.status, summary: x.status, detail: x.message, life: 1800 });
                        }, offset);
                        offset += 1800;
                    });
                })
                .catch((error) => {
                    const errResponse = error.response;
                    if (!errResponse.data.message) {
                        toast.current.show({ severity: "error", summary: "Internal Server Error", detail: error.response.statusText, life: 3000 });
                    } else {
                        toast.current.show({ severity: "warn", summary: "Warning", detail: error.response.data.message, life: 3000 });
                    }
                });
        }
    };

    const simpanSelectedPindahKelas = () => {
        const tambahArray = selectedStudents.map((x) => {
            return { id: x.id, idKelas: x.kelas.idKelas, tahunPelajaran, kelas: { idKelas: kelas.id, tahunPelajaran } };
        });
        setSubmitted(true);
        if (kelas) {
            studentService
                .putKelasStudents(tambahArray)
                .then((res) => {
                    setStudent(emptyStudent);
                    setKelasSekarang(null);
                    setSelectedStudents([]);
                    hideSelectedDialog();
                    let offset = 0;
                    return res.data.message.map((x, i) => {
                        setTimeout(() => {
                            toast.current.show({ severity: x.status, summary: x.status, detail: x.message, life: 1800 });
                        }, offset);
                        offset += 1800;
                    });
                })
                .catch((error) => {
                    const errResponse = error.response;
                    if (!errResponse.data.message) {
                        hideSelectedDialog();
                        toast.current.show({ severity: "error", summary: "Internal Server Error", detail: error.response.statusText, life: 3000 });
                    } else {
                        hideSelectedDialog();
                        toast.current.show({ severity: "warn", summary: "Warning", detail: error.response.data.message, life: 3000 });
                    }
                });
        }
    };

    const simpanTambahKelas = () => {
        setSubmitted(true);
        if (kelas) {
            studentService
                .postKelasStudent({ id: student.id, idKelas: kelas.id, tahunPelajaran })
                .then((res) => {
                    setStudent(emptyStudent);
                    setSelectedStudents(selctedEmpty);
                    setKelas(null);
                    setKelasSekarang(null);
                    hideStudent();
                    toast.current.show({ severity: "success", summary: "Successful", detail: res.data.message, life: 3000 });
                })
                .catch((error) => {
                    const errResponse = error.response;
                    if (!errResponse.data.message) {
                        toast.current.show({ severity: "error", summary: "Internal Server Error", detail: error.response.statusText, life: 3000 });
                    } else {
                        toast.current.show({ severity: "warn", summary: "Warning", detail: error.response.data.message, life: 3000 });
                    }
                });
        }
    };

    const simpanPindahKelas = () => {
        setSubmitted(true);
        if (kelas) {
            studentService
                .putKelasStudent({ id: student.id, idKelas: kelasSekarang.id, tahunPelajaran, kelas: { idKelas: kelas.id, tahunPelajaran } })
                .then((res) => {
                    setStudent(emptyStudent);
                    setSelectedStudents(selctedEmpty);
                    setKelasSekarang(null);
                    setKelas(null);
                    hideStudent();
                    toast.current.show({ severity: "success", summary: "Successful", detail: res.data.message, life: 3000 });
                })
                .catch((error) => {
                    const errResponse = error.response;
                    if (!errResponse.data.message) {
                        toast.current.show({ severity: "error", summary: "Internal Server Error", detail: error.response.statusText, life: 3000 });
                    } else {
                        toast.current.show({ severity: "warn", summary: "Warning", detail: error.response.data.message, life: 3000 });
                    }
                });
        }
    };

    const exportCSV = () => {
        // dt.current.exportCSV();
        sekolahService.putTpSekolah(selectedStudents);
    };

    const confirmDeleteSelected = () => {
        setDeleteKelasStudentsDialog(true);
    };

    const confirmTambahSelected = () => {
        setKelas(null);
        setSelectedDialog(true);
        setNameSelectedDialog("Tambah kelas");
    };
    const confirmPindahSelected = () => {
        setKelas(null);
        setSelectedDialog(true);
        setNameSelectedDialog("Pindah kelas");
    };

    const deleteKelasSelectedStudents = () => {
        studentService
            .deleteKelasStudents(selectedStudents)
            .then((res) => {
                setStudent(emptyStudent);
                setSelectedStudents([]);
                hideDeleteKelasStudentsDialog();
                toast.current.show({ severity: "success", summary: "Successful", detail: res.data.message, life: 3000 });
            })
            .catch((error) => {
                const errResponse = error.response;
                if (!errResponse.data.message) {
                    toast.current.show({ severity: "warn", summary: "Warning", detail: error.response.statusText, life: 3000 });
                } else {
                    toast.current.show({ severity: "error", summary: "Internal Server Error", detail: error.response.data.message, life: 3000 });
                }
            });
    };

    const leftToolbarTemplate = () => {
        return (
            <React.Fragment>
                <div className="my-2">
                    <Button label="Tambah" icon="pi pi-plus" className="p-button-success" onClick={confirmTambahSelected} disabled={selectedTambah.length || !selectedStudents.length} />{" "}
                    <Button label="Pindah" icon="pi pi-sign-in" className="p-button-warning" onClick={confirmPindahSelected} disabled={selectedPindah.length || !selectedStudents.length} />{" "}
                    <Button label="Bersihkan Kelas" icon="pi pi-trash" className="p-button-error" onClick={confirmDeleteSelected} disabled={selectedPindah.length || !selectedStudents.length} />
                </div>
            </React.Fragment>
        );
    };

    const rightToolbarTemplate = () => {
        return (
            <React.Fragment>
                {/* <FileUpload mode="basic" accept="xlsx/*" maxFileSize={1000000} label="Import" icon="pi pi-download" chooseLabel="Import" className="mr-2 inline-block" />
                <Button label="Export" icon="pi pi-download" className="p-button-help" onClick={exportCSV} /> */}
            </React.Fragment>
        );
    };

    const namaBodyTemplate = (rowData) => {
        return (
            <>
                <span className="p-column-title">Nama</span>
                {rowData.nama}
            </>
        );
    };

    const nisnBodyTemplate = (rowData) => {
        return (
            <>
                <span className="p-column-title">NISN</span>
                {rowData.nisn}
            </>
        );
    };

    const nisBodyTemplate = (rowData) => {
        return (
            <>
                <span className="p-column-title">NIS</span>
                {rowData.nis}
            </>
        );
    };

    const kelasBodyTemplate = (rowData) => {
        return (
            <>
                <span className="p-column-title">Kelas</span>
                {getNameClasses(rowData.kelas.idKelas)}
            </>
        );
    };

    const actionBodyTemplate = (rowData) => {
        return (
            <div className="actions">
                {!rowData.kelas.idKelas ? <Button icon="pi pi-plus" className="p-button-rounded p-button-success mr-2" onClick={() => tambahKelas(rowData)} /> : <Button icon="pi pi-sign-in" className="p-button-rounded p-button-warning" onClick={() => pindahKelas(rowData)} />}{" "}
                {!rowData.kelas.idKelas ? "" : <Button icon="pi pi-trash" className="p-button-rounded p-button-error" onClick={() => bukaHapusKelas(rowData)} />}
            </div>
        );
    };

    const header = (
        <div className="flex flex-column md:flex-row md:justify-content-between md:align-items-center">
            <h5 className="m-0">Data Siswa</h5>
            <span className="block mt-2 md:mt-0 p-input-icon-left">
                <i className="pi pi-search" />
                <InputText type="search" onInput={(e) => setGlobalFilter(e.target.value)} placeholder="Search..." />
            </span>
        </div>
    );

    const studentDialogFooter = (
        <>
            <Button label="Cancel" icon="pi pi-times" className="p-button-text" onClick={hideStudent} />
            <Button label="Save" icon="pi pi-check" className="p-button-text" onClick={nameDialog === "Tambah Kelas" ? simpanTambahKelas : simpanPindahKelas} />
        </>
    );

    const selectedDialogFooter = (
        <>
            <Button label="Cancel" icon="pi pi-times" className="p-button-text" onClick={hideSelectedDialog} />
            <Button label="Save" icon="pi pi-check" className="p-button-text" onClick={nameSelectedDialog === "Tambah kelas" ? simpanSelectedTambahKelas : simpanSelectedPindahKelas} />
        </>
    );

    const hapusKelasDialogFooter = (
        <>
            <Button label="Cancel" icon="pi pi-times" className="p-button-text" onClick={hideHapusKelasDialog} />
            <Button label="Save" icon="pi pi-check" className="p-button-text" onClick={hapusKelas} />
        </>
    );
    const deleteKelasStudentsDialogFooter = (
        <>
            <Button label="No" icon="pi pi-times" className="p-button-text" onClick={hideDeleteKelasStudentsDialog} />
            <Button label="Yes" icon="pi pi-check" className="p-button-text" onClick={deleteKelasSelectedStudents} />
        </>
    );

    return (
        <div className="grid crud-demo">
            <div className="col-12">
                <div className="card">
                    <Toast ref={toast} />
                    <Toolbar className="mb-4" left={leftToolbarTemplate} right={rightToolbarTemplate}></Toolbar>

                    <DataTable
                        ref={dt}
                        value={students}
                        selection={selectedStudents}
                        onSelectionChange={(e) => setSelectedStudents(e.value)}
                        dataKey="id"
                        paginator
                        rows={10}
                        rowsPerPageOptions={[5, 10, 25]}
                        className="datatable-responsive"
                        paginatorTemplate="FirstPageLink PrevPageLink PageLinks NextPageLink LastPageLink CurrentPageReport RowsPerPageDropdown"
                        currentPageReportTemplate="Menampilkan {first} sampai {last} dari total {totalRecords} siswa"
                        globalFilter={globalFilter}
                        emptyMessage="Siswa masih kosong."
                        header={header}
                    >
                        <Column selectionMode="multiple" headerStyle={{ width: "3rem" }}></Column>
                        <Column field="nisn" header="NISN" sortable body={nisnBodyTemplate}></Column>
                        <Column field="nis" header="NIS" sortable body={nisBodyTemplate}></Column>
                        <Column field="nama" header="Nama" sortable body={namaBodyTemplate}></Column>
                        <Column field="kotaLahir" header="Kelas" sortable body={kelasBodyTemplate}></Column>
                        <Column header="Aksi" body={actionBodyTemplate}></Column>
                    </DataTable>

                    <Dialog visible={studentDialog} style={{ width: "450px" }} header={nameDialog} modal className="p-fluid" footer={studentDialogFooter} onHide={hideStudent}>
                        <div className="field">
                            <label htmlFor="kelas">Kelas</label>
                            <Dropdown id="kelas" autoFocus options={kelasDropdown} value={kelas} onChange={(e) => setKelas(e.value)} placeholder="Pilih satu" optionLabel="name" className={classNames({ "p-invalid": submitted && !kelas })}></Dropdown>
                            {submitted && !kelas && <small className="p-invalid">Kelas tidak boleh kosong.</small>}
                        </div>
                    </Dialog>
                    <Dialog visible={deleteKelasStudentsDialog} style={{ width: "450px" }} header="Confirm" modal footer={deleteKelasStudentsDialogFooter} onHide={hideDeleteKelasStudentsDialog}>
                        <div className="flex align-items-center justify-content-center">
                            <i className="pi pi-exclamation-triangle mr-3" style={{ fontSize: "2rem" }} />
                            {students && <span>Kamu yakin akan menghapus kelas pada siswa yang ditandai?</span>}
                        </div>
                    </Dialog>
                    <Dialog visible={hapusKelasDialog} style={{ width: "450px" }} header="Confirm" modal footer={hapusKelasDialogFooter} onHide={hideHapusKelasDialog}>
                        <div className="flex align-items-center justify-content-center">
                            <i className="pi pi-exclamation-triangle mr-3" style={{ fontSize: "2rem" }} />
                            {student && kelas && (
                                <span>
                                    Anda yakin akan menghapus kelas <b>{kelas.name}</b> di siswa <b>{student.nama}</b>?
                                </span>
                            )}
                        </div>
                    </Dialog>
                    <Dialog visible={selectedDialog} style={{ width: "450px" }} header={nameSelectedDialog} modal className="p-fluid" footer={selectedDialogFooter} onHide={hideSelectedDialog}>
                        <div className="field">
                            <label htmlFor="kelas">Kelas</label>
                            <Dropdown id="kelas" autoFocus options={kelasDropdown} value={kelas} onChange={(e) => setKelas(e.value)} placeholder="Pilih satu" optionLabel="name" className={classNames({ "p-invalid": submitted && !kelas })}></Dropdown>
                            {submitted && !kelas && <small className="p-invalid">Kelas tidak boleh kosong.</small>}
                        </div>
                    </Dialog>
                </div>
            </div>
        </div>
    );
};
