import axios from "axios";

export default class TransaksiService {
    lihatSemuaPembayaran() {
        return axios.get("/api/pembayaran");
    }
    lihatPembayaranSiswa(payload) {
        const { tahunPelajaran, idSiswa } = payload;
        return axios.get(`/api/pembayaran/siswa?tahunPelajaran=${tahunPelajaran}&idSiswa=${idSiswa}`);
    }
    lihatPembayaranTahun(payload) {
        return axios.get(`/api/pembayaran/transaksi/laporan?tahunPelajaran=${payload}`);
    }
    lihatSubPembayaranSiswa(payload) {
        return axios({
            method: "post",
            url: "/api/pembayaran/siswa",
            data: payload,
        });
    }
    hapusTransaksi(payload) {
        return axios.delete("/api/pembayaran/transaksi/" + payload);
    }
    tambahTransaksi(payload) {
        return axios({
            method: "post",
            url: "/api/pembayaran/transaksi",
            data: payload,
        });
    }
}
