import axios from "axios";

export default class PembayaranService {
    lihatSemuaPembayaran() {
        return axios.get("/api/pembayaran");
    }

    tambahPembayaran(payload) {
        const { namaPembayaran, tahunPelajaran, idKelas } = payload;
        return axios({ method: "post", url: "/api/pembayaran", data: { namaPembayaran, tahunPelajaran, idKelas } });
    }

    editPembayaran(payload) {
        const { _id, namaPembayaran, tahunPelajaran, idKelas } = payload;
        return axios({ method: "put", url: "/api/pembayaran", data: { _id, namaPembayaran, tahunPelajaran, idKelas } });
    }

    tambahSubPembayaran(payload) {
        const { idPembayaran: _id, namaSub, total } = payload;
        return axios({ method: "post", url: "/api/pembayaran/sub", data: { _id, namaSub, total } });
    }

    hapusPembayaran(id) {
        return axios.delete(`/api/pembayaran/${id}`);
    }

    hapusSubPembayaran(id) {
        return axios.delete(`/api/pembayaran/sub/${id}`);
    }
}
