import React, { useState, useEffect, useRef } from "react";
import { Link } from "react-router-dom";
import classNames from "classnames";
import { Dialog } from "primereact/dialog";
import { Dropdown } from "primereact/dropdown";
import { Button } from "primereact/button";
import SekolahService from "./service/SekolahService";
import { Toast } from "primereact/toast";

export const AppTopbar = (props) => {
    const empty = [];
    const [_id, setId] = useState(null);
    const [logo, setLogo] = useState(null);
    const [tahunPelajaran, setTahunPelajaran] = useState(null);
    const [tahunPelajaranItem, setTahunPelajaranItem] = useState(empty);
    const [settingDialog, setSettingDialog] = useState(false);
    const [submitted, setSubmited] = useState(false);
    const toast = useRef(null);
    const sekolahService = new SekolahService();

    useEffect(() => {
        const fetchTpSekolah = async () => {
            const getTpSekolah = await sekolahService.getTpSekolah();
            setTahunPelajaranItem(getTpSekolah.data.data.tahunPelajaran);
            setId(getTpSekolah.data.data._id);
            setTahunPelajaran({ tahunPelajaran: getTpSekolah.data.data.tahunPelajaranSekarang });
            setLogo(getTpSekolah.data.data.logo);
        };
        fetchTpSekolah();
    }, [settingDialog]);

    const saveTahunPelajaran = async () => {
        setSubmited(true);
        if (tahunPelajaran) {
            const tahunPelajaranSekarang = tahunPelajaran.tahunPelajaran;
            await sekolahService
                .putTpSekolah({ _id, tahunPelajaranSekarang })
                .then((res) => {
                    toast.current.show({ severity: "success", summary: "Successful", detail: res.data.message, life: 3000 });
                    hideSetting();
                    setTimeout(() => {
                        window.location.reload();
                    }, 3000);
                })
                .catch((error) => {
                    const errResponse = error.response;
                    if (!errResponse.data.message) {
                        toast.current.show({ severity: "error", summary: "Internal Server Error", detail: error.response.statusText, life: 3000 });
                    } else {
                        toast.current.show({ severity: "warn", summary: "Warning", detail: error.response.data.message, life: 3000 });
                    }
                });
        }
    };

    const openSetting = () => {
        setSubmited(false);
        setSettingDialog(true);
    };

    const hideSetting = () => {
        setSettingDialog(false);
    };
    const settingDialogFooter = () => {
        return (
            <div>
                <Button label="Batal" icon="pi pi-times" onClick={hideSetting} />
                <Button label="Simpan" icon="pi pi-check" onClick={() => saveTahunPelajaran()} className="p-button-text" />
            </div>
        );
    };
    return (
        <div className="layout-topbar">
            <Link to="/" className="layout-topbar-logo">
                <img src={props.layoutColorMode === "light" ? logo : logo} alt="logo" />
                <span>SMK {tahunPelajaran && tahunPelajaran.tahunPelajaran}</span>
            </Link>

            <Toast ref={toast} />
            <button type="button" className="p-link  layout-menu-button layout-topbar-button" onClick={props.onToggleMenuClick}>
                <i className="pi pi-bars" />
            </button>

            <button type="button" className="p-link layout-topbar-menu-button layout-topbar-button" onClick={props.onMobileTopbarMenuClick}>
                <i className="pi pi-ellipsis-v" />
            </button>

            <ul className={classNames("layout-topbar-menu lg:flex origin-top", { "layout-topbar-menu-mobile-active": props.mobileTopbarMenuActive })}>
                <li>
                    <button onClick={openSetting} className="p-link layout-topbar-button">
                        <i className="pi pi-cog" />
                        <span>Pengaturan</span>
                    </button>
                </li>
            </ul>
            <Dialog header="Setting Tahun Pelajaran" visible={settingDialog} position={"top-right"} style={{ width: "450px" }} footer={settingDialogFooter} onHide={hideSetting}>
                <div className="p-fluid">
                    <div className="field grid">
                        <label htmlFor="tahunPelajaran" className="col-12 mb-2 md:col-6 md:mb-0">
                            Tahun Pelajaran Aktif
                        </label>
                        <div className="col-12 md:col-6">
                            <Dropdown id="tahunPelajaran" autoFocus value={tahunPelajaran} onChange={(e) => setTahunPelajaran(e.value)} options={tahunPelajaranItem} optionLabel="tahunPelajaran" placeholder="Pilih satu" className={classNames({ "p-invalid": submitted && !tahunPelajaran })}></Dropdown>
                            {submitted && !tahunPelajaran && <small className="p-invalid">Tahun pelajaran tidak boleh kosong.</small>}
                        </div>
                    </div>
                </div>
            </Dialog>
        </div>
    );
};
