import axios from "axios";

export default class StudentService {
    getStudents() {
        return axios.get("/api/siswa");
    }
    putStudents(students) {
        return axios({
            method: "put",
            url: "/api/siswa",
            data: students,
        });
    }
    postStudents(students) {
        return axios({
            method: "post",
            url: "/api/siswa",
            data: students,
        });
    }
    postKelasStudent(students) {
        return axios({
            method: "post",
            url: "/api/siswa/kelas",
            data: students,
        });
    }
    postKelasStudents(students) {
        return axios({
            method: "post",
            url: "/api/siswa/kelass",
            data: students,
        });
    }
    putKelasStudents(students) {
        return axios({
            method: "put",
            url: "/api/siswa/kelass",
            data: students,
        });
    }
    deleteKelasStudent(students) {
        return axios({
            method: "delete",
            url: "/api/siswa/kelas",
            data: students,
        });
    }
    putKelasStudent(students) {
        return axios({
            method: "put",
            url: "/api/siswa/kelas",
            data: students,
        });
    }
    deleteStudents(...students) {
        return axios({
            method: "delete",
            url: "/api/siswa",
            data: students,
        });
    }
    deleteKelasStudents(students) {
        return axios({
            method: "delete",
            url: "/api/siswa/kelass",
            data: students,
        });
    }
    deleteStudent(id) {
        return axios.delete("/api/siswa/" + id);
    }
    postSiswaTp(tahunPelajaran) {
        return axios(
            {
                method: "post",
                url: "/api/siswa/tahun",
                data: { tahunPelajaran },
            },
            {
                headers: {
                    "Content-disposition": "attachment; filename=[yourFileName]",
                    "Content-Type": "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet",
                },
            }
        );
    }
}
