import axios from "axios";

export default class LaporanService {
    static dashboard(tahunPelajaran) {
        return axios.get(`/api/laporan/dashboard?tahunPelajaran=${tahunPelajaran}`);
    }
    static bar(selectTahun) {
        return axios.get(`/api/laporan/bar?selectTahun=${selectTahun}`);
    }
}
