import React, { useState, useEffect, useRef } from "react";
import classNames from "classnames";
import { DataTable } from "primereact/datatable";
import { Column } from "primereact/column";
import { Toast } from "primereact/toast";
import { Button } from "primereact/button";
import { Toolbar } from "primereact/toolbar";
import { Dialog } from "primereact/dialog";
import { InputText } from "primereact/inputtext";
import { Dropdown } from "primereact/dropdown";
import { ClassesService } from "../service/ClassesService";

export const Classes = () => {
    const emptyClass = {
        name: "",
        level: "",
        major: "",
    };

    const emptyMajor = [];
    const emptyDataMajor = {
        major: null,
        code: null,
    };

    const levels = [{ level: 10 }, { level: 11 }, { level: 12 }];

    const [dataClass, setDataClass] = useState(emptyClass);
    const [dataMajor, setDataMajor] = useState(emptyDataMajor);
    const [classesDialog, setClassesDialog] = useState(false);
    const [majorDialog, setMajorDialog] = useState(false);
    const [deleteClassesDialog, setDeleteClassesDialog] = useState(false);
    const [deleteMajorDialog, setDeleteMajorDialog] = useState(false);
    const [classes, setClasses] = useState(false);
    const [majors, setMajor] = useState(emptyMajor);
    const [submitted, setSubmitted] = useState(false);
    const [levelValue, setLevelValue] = useState(null);
    const [majorValue, setMajorValue] = useState(null);
    const [classValue, setClassValue] = useState(emptyClass);
    const toast = useRef(null);

    useEffect(() => {
        const fetchData = async () => {
            const classes = new ClassesService();
            const getDataClasses = await classes.getAllClasses();
            setClasses(getDataClasses.data.data);
            const getDataMajors = await classes.getAllMajor();
            setMajor(getDataMajors.data.data);
        };
        fetchData();
    }, [classesDialog, classValue, majorValue, dataMajor]);

    const saveClasses = async () => {
        setSubmitted(true);
        const newClass = new ClassesService();
        if (dataClass.id) {
            newClass
                .putClasses(dataClass)
                .then((res) => {
                    setDataClass(emptyClass);
                    setLevelValue(null);
                    setMajorValue(null);
                    toast.current.show({ severity: "success", summary: "Successful", detail: res.data.message, life: 3000 });
                    hideDialog();
                })
                .catch((error) => {
                    const errResponse = error.response;
                    if (!errResponse.data.message) {
                        toast.current.show({ severity: "error", summary: "Internal Server Error", detail: error.response.statusText, life: 3000 });
                    } else {
                        toast.current.show({ severity: "warn", summary: "Warning", detail: error.response.data.message, life: 3000 });
                    }
                });
        } else {
            newClass
                .postClasses(dataClass)
                .then((res) => {
                    setDataClass(emptyClass);
                    setLevelValue(null);
                    setMajorValue(null);
                    toast.current.show({ severity: "success", summary: "Successful", detail: res.data.message, life: 3000 });
                    hideDialog();
                })
                .catch((error) => {
                    const errResponse = error.response;
                    if (!errResponse.data.message) {
                        toast.current.show({ severity: "error", summary: "Internal Server Error", detail: error.response.statusText, life: 3000 });
                    } else {
                        toast.current.show({ severity: "warn", summary: "Warning", detail: error.response.data.message, life: 3000 });
                    }
                });
        }
    };

    const openNew = () => {
        setSubmitted(false);
        setClassesDialog(true);
    };

    const editClasses = async (data) => {
        const getMajorById = new ClassesService();
        const majors = await getMajorById.getMajorById(data.major);
        const major = majors.data.data[0];
        setDataClass(data);
        setLevelValue({ level: data.level });
        setMajorValue({ ...major });
        setSubmitted(false);
        setClassesDialog(true);
    };

    const openNewMajor = () => {
        setSubmitted(false);
        setMajorDialog(true);
    };

    const hideDialog = () => {
        setSubmitted(false);
        setClassesDialog(false);
        setLevelValue(null);
        setMajorValue(null);
        setDataClass(emptyClass);
    };

    const hideMajorDialog = () => {
        setSubmitted(false);
        setMajorDialog(false);
        setMajorValue(null);
    };

    const saveMajor = () => {
        setSubmitted(true);
        const newMajor = new ClassesService();
        newMajor
            .postMajor(dataMajor)
            .then((res) => {
                setDataMajor(emptyDataMajor);
                toast.current.show({ severity: "success", summary: "Successful", detail: res.data.message, life: 3000 });
                hideMajorDialog();
            })
            .catch((error) => {
                const errResponse = error.response;
                if (!errResponse.data.message) {
                    toast.current.show({ severity: "error", summary: "Internal Server Error", detail: error.response.statusText, life: 3000 });
                } else {
                    toast.current.show({ severity: "warn", summary: "Warning", detail: error.response.data.message, life: 3000 });
                }
            });
    };

    const onInputChange = (e, name) => {
        const val = (e.target && e.target.value) || "";
        let _dataClass = { ...dataClass };
        _dataClass[`${name}`] = val;

        setDataClass(_dataClass);
    };

    const onMajorInputChange = (e, name) => {
        const val = (e.target && e.target.value) || "";
        let _dataMajor = { ...dataMajor };
        _dataMajor[`${name}`] = val;

        setDataMajor(_dataMajor);
    };

    const onChangeLevel = (e) => {
        const val = (e.target && e.target.value) || "";
        let _dataClass = { ...dataClass };
        _dataClass.level = val.level;

        setLevelValue(val);
        setDataClass(_dataClass);
    };

    const onChangeMajor = (e) => {
        const val = (e.target && e.target.value) || "";
        let _dataClass = { ...dataClass };
        _dataClass.major = val.code;

        setMajorValue(val);
        setDataClass(_dataClass);
    };

    const confirmDeleteClasses = (data) => {
        setClassValue(data);
        setDeleteClassesDialog(true);
    };

    const confirmDeleteMajor = (data) => {
        setMajorValue(data);
        setDeleteMajorDialog(true);
    };

    const classesDialogFooter = (
        <>
            <Button label="Cancel" icon="pi pi-times" className="p-button-text" onClick={hideDialog} />
            <Button label="Save" icon="pi pi-check" className="p-button-text" onClick={saveClasses} />
        </>
    );

    const majorDialogFooter = (
        <>
            <Button label="Cancel" icon="pi pi-times" className="p-button-text" onClick={hideMajorDialog} />
            <Button label="Save" icon="pi pi-check" className="p-button-text" onClick={saveMajor} />
        </>
    );
    const leftToolbarTemplate = () => {
        return (
            <React.Fragment>
                <div className="my-2">
                    <Button label="New" icon="pi pi-plus" className="p-button-success mr-2" onClick={openNew} />
                </div>
            </React.Fragment>
        );
    };

    const rightToolbarTemplate = () => {
        return (
            <React.Fragment>
                <div className="my-2">
                    <Button label="New" icon="pi pi-plus" className="p-button-success mr-2" onClick={openNewMajor} />
                </div>
            </React.Fragment>
        );
    };

    const hideDeleteClassesDialog = () => {
        setClassValue(emptyClass);
        setDeleteClassesDialog(false);
    };
    const hideDeleteMajorDialog = () => {
        setMajorValue(emptyMajor);
        setDeleteMajorDialog(false);
    };
    const deleteClasses = () => {
        const deleteClass = new ClassesService();
        deleteClass
            .deleteClasses(classValue.id)
            .then((res) => {
                setDeleteClassesDialog(false);
                setClassValue(emptyClass);
                toast.current.show({ severity: "success", summary: "Successful", detail: res.data.message, life: 3000 });
                hideDeleteClassesDialog();
            })
            .catch((error) => {
                const errResponse = error.response;
                if (!errResponse.data.message) {
                    toast.current.show({ severity: "error", summary: "Internal Server Error", detail: error.response.statusText, life: 3000 });
                } else {
                    toast.current.show({ severity: "warn", summary: "Warning", detail: error.response.data.message, life: 3000 });
                }
            });
    };
    const deleteMajor = () => {
        const deleteMajor = new ClassesService();
        deleteMajor
            .deleteMajor(majorValue.code)
            .then((res) => {
                setDeleteMajorDialog(false);
                setMajorValue(emptyClass);
                toast.current.show({ severity: "success", summary: "Successful", detail: res.data.message, life: 3000 });
                hideDeleteMajorDialog();
            })
            .catch((error) => {
                const errResponse = error.response;
                if (!errResponse.data.message) {
                    toast.current.show({ severity: "error", summary: "Internal Server Error", detail: error.response.statusText, life: 3000 });
                } else {
                    toast.current.show({ severity: "warn", summary: "Warning", detail: error.response.data.message, life: 3000 });
                }
            });
    };

    const deleteClassesDialogFooter = (
        <>
            <Button label="No" icon="pi pi-times" className="p-button-text" onClick={hideDeleteClassesDialog} />
            <Button label="Yes" icon="pi pi-check" className="p-button-text" onClick={deleteClasses} />
        </>
    );
    const deleteMajorDialogFooter = (
        <>
            <Button label="No" icon="pi pi-times" className="p-button-text" onClick={hideDeleteMajorDialog} />
            <Button label="Yes" icon="pi pi-check" className="p-button-text" onClick={deleteMajor} />
        </>
    );

    return (
        <div className="grid">
            <div className="col-12 xl:col-6">
                <div className="card">
                    <h5>Data Kelas</h5>
                    <Toast ref={toast} />
                    <Toolbar className="mb-4" left={leftToolbarTemplate}></Toolbar>
                    <DataTable value={classes} className="p-datatable-customers p-text-center" rows={5} paginator>
                        <Column field="name" header="Nama kelas" body={(data) => data.name} />
                        <Column field="level" header="Tingkat" sortable body={(data) => data.level} />
                        <Column field="major" header="Jurusan" sortable body={(data) => data.major} />
                        <Column
                            header="Aksi"
                            body={(data) => (
                                <>
                                    <Button icon="pi pi-pencil" type="button" className="p-button-rounded p-button-warning p-button-text p-mr-2" onClick={() => editClasses(data)} />
                                    <Button icon="pi pi-trash" type="button" className="p-button-rounded p-button-error p-button-text" onClick={() => confirmDeleteClasses(data)} />
                                </>
                            )}
                            className="p-text-center"
                        />
                    </DataTable>
                    <Dialog visible={classesDialog} style={{ width: "450px" }} header="Detail Kelas" modal className="p-fluid" footer={classesDialogFooter} onHide={hideDialog}>
                        <div className="field">
                            <label htmlFor="name">Nama Kelas</label>
                            <InputText id="name" value={dataClass.name} onChange={(e) => onInputChange(e, "name")} required autoFocus className={classNames({ "p-invalid": submitted && !dataClass.name })} />
                            {submitted && !dataClass.name && <small className="p-invalid">Nama kelas tidak boleh kosong.</small>}
                        </div>
                        <div className="field">
                            <label htmlFor="level">Tingkat</label>
                            <Dropdown value={levelValue} onChange={(e) => onChangeLevel(e)} options={levels} optionLabel="level" placeholder="Select" required className={classNames({ "p-invalid": submitted && levelValue === null })} />
                            {submitted && levelValue === null && <small className="p-invalid">Tingkat tidak boleh kosong.</small>}
                        </div>
                        <div className="field">
                            <label htmlFor="major">Jurusan</label>
                            <Dropdown value={majorValue} onChange={(e) => onChangeMajor(e)} options={majors} optionLabel="major" placeholder="Select" required className={classNames({ "p-invalid": submitted && majorValue === null })} />
                            {submitted && majorValue === null && <small className="p-invalid">Jurusan tidak boleh kosong.</small>}
                        </div>
                    </Dialog>
                    <Dialog visible={deleteClassesDialog} style={{ width: "450px" }} header="Konfirmasi" modal footer={deleteClassesDialogFooter} onHide={hideDeleteClassesDialog}>
                        <div className="flex align-items-center justify-content-center">
                            <i className="pi pi-exclamation-triangle mr-3" style={{ fontSize: "2rem" }} />
                            {classValue && (
                                <span>
                                    Anda yakin akan menghapus kelas <b>{classValue.name}</b>?
                                </span>
                            )}
                        </div>
                    </Dialog>
                </div>
            </div>

            <div className="col-12 xl:col-6">
                <div className="card">
                    <h5>Data Jurusan</h5>
                    <Toolbar className="mb-4" left={rightToolbarTemplate}></Toolbar>
                    <DataTable value={majors} className="p-datatable-customers p-text-center" rows={5} paginator>
                        <Column field="name" header="Nama jurusan" body={(data) => data.major} />
                        <Column field="level" header="Kode" body={(data) => data.code} />
                        <Column
                            header="Aksi"
                            body={(data) => (
                                <>
                                    <Button icon="pi pi-trash" type="button" className="p-button-rounded p-button-error p-button-text" onClick={() => confirmDeleteMajor(data)} />
                                </>
                            )}
                            className="p-text-center"
                        />
                    </DataTable>
                    <Dialog visible={majorDialog} style={{ width: "450px" }} header="Detail Jurusan" modal className="p-fluid" footer={majorDialogFooter} onHide={hideMajorDialog}>
                        <div className="field">
                            <label htmlFor="name">Nama Jurusan</label>
                            <InputText id="name" value={dataMajor.major} onChange={(e) => onMajorInputChange(e, "major")} required autoFocus className={classNames({ "p-invalid": submitted && !dataMajor.major })} />
                            {submitted && !dataMajor.major && <small className="p-invalid">Nama jurusan tidak boleh kosong.</small>}
                        </div>
                        <div className="field">
                            <label htmlFor="name">Kode Jurusan</label>
                            <InputText id="name" placeholder="contoh: TBSM" value={dataMajor.code} onChange={(e) => onMajorInputChange(e, "code")} required className={classNames({ "p-invalid": submitted && !dataMajor.code })} />
                            {submitted && !dataMajor.code && <small className="p-invalid">Kode jurusan tidak boleh kosong.</small>}
                        </div>
                    </Dialog>
                    <Dialog visible={deleteMajorDialog} style={{ width: "450px" }} header="Konfirmasi" modal footer={deleteMajorDialogFooter} onHide={hideDeleteMajorDialog}>
                        <div className="flex align-items-center justify-content-center">
                            <i className="pi pi-exclamation-triangle mr-3" style={{ fontSize: "2rem" }} />
                            {majorValue && (
                                <span>
                                    Anda yakin akan menghapus jurusan <b>{majorValue.major}</b>?
                                </span>
                            )}
                        </div>
                    </Dialog>
                </div>
            </div>
        </div>
    );
};
