import React, { useState, useEffect, useRef } from "react";
import classNames from "classnames";
import { Dialog } from "primereact/dialog";
import { DataTable } from "primereact/datatable";
import { Column } from "primereact/column";
import { Toast } from "primereact/toast";
import { InputText } from "primereact/inputtext";
import { Button } from "primereact/button";
import { InputTextarea } from "primereact/inputtextarea";
import SekolahService from "../service/SekolahService";

const sekolahService = new SekolahService();
export const School = () => {
    const sekolahEmpty = {
        _id: "",
        namaSekolah: "",
        email: "",
        telepon: "",
        alamatSekolah: "",
        kepsek: "",
        logo: "",
    };
    const [sekolah, setSekolah] = useState(sekolahEmpty);
    const [submitted, setSubmited] = useState(false);
    const [tahunPelajaranDialog, setTahunPelajaranDialog] = useState(false);
    const [tahunPelajaran, setTahunPelajaran] = useState("");
    const [tahunPelajaranItem, setTahunPelajaranItem] = useState([]);
    const [hapusTahunPelajaranDialog, setHapusTahunPelajaranDialog] = useState(false);
    const toast = useRef(null);

    useEffect(() => {
        setSubmited(false);
        const fetchData = async () => {
            const getSekolah = await sekolahService.getSekolah();
            if (getSekolah.data.data) {
                setSekolah(getSekolah.data.data);
            }
            const getTpSekolah = await sekolahService.getTpSekolah();
            setTahunPelajaranItem(getTpSekolah.data.data.tahunPelajaran);
        };
        fetchData();
    }, [submitted, tahunPelajaranDialog, hapusTahunPelajaranDialog]);

    const onInputChange = (e, name) => {
        const val = (e.target && e.target.value) || "";
        let _sekolah = { ...sekolah };
        _sekolah[`${name}`] = val;

        setSekolah(_sekolah);
    };

    const saveSchool = async () => {
        setSubmited(true);

        if (sekolah) {
            await sekolahService
                .putSekolah(sekolah)
                .then((res) => {
                    setSubmited(false);
                    toast.current.show({ severity: "success", summary: "Successful", detail: res.data.message, life: 3000 });
                })
                .catch((error) => {
                    const errResponse = error.response;
                    if (!errResponse.data.message) {
                        toast.current.show({ severity: "error", summary: "Internal Server Error", detail: error.response.statusText, life: 3000 });
                    } else {
                        toast.current.show({ severity: "warn", summary: "Warning", detail: error.response.data.message, life: 3000 });
                    }
                });
        }
    };
    const saveTahunPelajaran = async () => {
        if (tahunPelajaran !== "") {
            await sekolahService
                .postTpSekolah({ _id: sekolah._id, tahunPelajaran })
                .then((res) => {
                    toast.current.show({ severity: "success", summary: "Successful", detail: res.data.message, life: 3000 });
                    hideTahunPelajaran();
                })
                .catch((error) => {
                    const errResponse = error.response;
                    if (!errResponse.data.message) {
                        toast.current.show({ severity: "error", summary: "Internal Server Error", detail: error.response.statusText, life: 3000 });
                    } else {
                        toast.current.show({ severity: "warn", summary: "Warning", detail: error.response.data.message, life: 3000 });
                    }
                });
        }
    };
    const hapusTahunPelajaran = async () => {
        if (tahunPelajaran !== "") {
            await sekolahService
                .deleteTpSekolah({ _id: sekolah._id, tahunPelajaran })
                .then((res) => {
                    toast.current.show({ severity: "success", summary: "Successful", detail: res.data.message, life: 3000 });
                    hideHapusTahunPelajaranDialog();
                })
                .catch((error) => {
                    const errResponse = error.response;
                    if (!errResponse.data.message) {
                        toast.current.show({ severity: "error", summary: "Internal Server Error", detail: error.response.statusText, life: 3000 });
                    } else {
                        toast.current.show({ severity: "warn", summary: "Warning", detail: error.response.data.message, life: 3000 });
                    }
                });
        }
    };
    const bukaTambahTahun = () => {
        setTahunPelajaranDialog(true);
        setTahunPelajaran("");
    };
    const bukaHapusTahunPelajaranDialog = (data) => {
        setTahunPelajaran(data.tahunPelajaran);
        setHapusTahunPelajaranDialog(true);
    };
    const hideTahunPelajaran = () => {
        setTahunPelajaranDialog(false);
    };
    const hideHapusTahunPelajaranDialog = () => {
        setTahunPelajaran("");
        setHapusTahunPelajaranDialog(false);
    };
    const tahunPelajaranDialogFooter = () => {
        return (
            <div>
                <Button label="Batal" icon="pi pi-times" onClick={hideTahunPelajaran} />
                <Button label="Simpan" icon="pi pi-check" onClick={() => saveTahunPelajaran()} className="p-button-text" />
            </div>
        );
    };
    const hapusTahunPelajaranDialogFooter = () => {
        return (
            <div>
                <Button label="Batal" icon="pi pi-times" onClick={hideHapusTahunPelajaranDialog} />
                <Button label="Simpan" icon="pi pi-check" onClick={() => hapusTahunPelajaran()} className="p-button-text" />
            </div>
        );
    };
    return (
        <div className="grid">
            <div className="col-12 xl:col-8">
                <div className="card">
                    <h5>Data Sekolah</h5>
                    <Toast ref={toast} />
                    <div className="p-fluid formgrid grid">
                        <div className="field col-12 md:col-12">
                            <label htmlFor="firstname2">Nama Sekolah</label>
                            <InputText id="firstname2" type="text" value={sekolah.namaSekolah} onChange={(e) => onInputChange(e, "namaSekolah")} required className={classNames({ "p-invalid": submitted && !sekolah.namaSekolah })} />
                            {submitted && !sekolah.namaSekolah && <small className="p-invalid">Nama sekolah tidak boleh kosong.</small>}
                        </div>
                        <div className="field col-12">
                            <label htmlFor="address">Alamat</label>
                            <InputTextarea rows={4} id="address" type="text" value={sekolah.alamatSekolah} onChange={(e) => onInputChange(e, "alamatSekolah")} required className={classNames({ "p-invalid": submitted && !sekolah.alamatSekolah })} />
                            {submitted && !sekolah.alamatSekolah && <small className="p-invalid">Alamat sekolah tidak boleh kosong.</small>}
                        </div>
                        <div className="field col-12 md:col-12">
                            <label htmlFor="firstname2">Nama Kepala Sekolah</label>
                            <InputText id="firstname2" type="text" value={sekolah.kepsek} onChange={(e) => onInputChange(e, "kepsek")} required className={classNames({ "p-invalid": submitted && !sekolah.kepsek })} />
                            {submitted && !sekolah.kepsek && <small className="p-invalid">Nama sekolah tidak boleh kosong.</small>}
                        </div>
                        <div className="field col-12 md:col-4">
                            <label htmlFor="email">Email</label>
                            <InputText id="email" type="text" value={sekolah.email} onChange={(e) => onInputChange(e, "email")} required className={classNames({ "p-invalid": submitted && !sekolah.email })} />
                            {submitted && !sekolah.email && <small className="p-invalid">Email sekolah tidak boleh kosong.</small>}
                        </div>
                        <div className="field col-12 md:col-3">
                            <label htmlFor="zip">Telepon</label>
                            <InputText id="firstname2" type="text" value={sekolah.telepon} onChange={(e) => onInputChange(e, "telepon")} required className={classNames({ "p-invalid": submitted && !sekolah.telepon })} />
                            {submitted && !sekolah.telepon && <small className="p-invalid">Telepon sekolah tidak boleh kosong.</small>}
                        </div>
                        <div className="field mb-5 col-12 md:col-5">
                            <label htmlFor="telepon">Logo</label>
                            <InputText id="telepon" type="text" value={sekolah.logo} onChange={(e) => onInputChange(e, "logo")} required className={classNames({ "p-invalid": submitted && !sekolah.logo })} />
                            {submitted && !sekolah.logo && <small className="p-invalid">Logo sekolah tidak boleh kosong.</small>}
                            <div className="flex flex-column mt-2 align-items-center">
                                <img style={{ width: "100px" }} src={sekolah.logo} alt="logo" />
                            </div>
                        </div>
                        <Button label="Simpan" onClick={() => saveSchool()} />
                    </div>
                </div>
            </div>

            <div className="col-12 xl:col-4">
                <div className="card">
                    <h5>Tahun Pelajaran</h5>
                    <Button label="Tambah" icon="pi pi-plus" className="mb-2 p-button-success" onClick={() => bukaTambahTahun()} />
                    <DataTable value={tahunPelajaranItem} className="p-datatable-customers p-text-center" rows={5} paginator>
                        <Column field="name" header="Tahun" body={(data) => data.tahunPelajaran} />
                        <Column
                            header="Aksi"
                            body={(data) => (
                                <>
                                    <Button icon="pi pi-trash" type="button" onClick={() => bukaHapusTahunPelajaranDialog(data)} className="p-button-rounded p-button-error p-button-text" />
                                </>
                            )}
                            className="p-text-center"
                        />
                    </DataTable>
                </div>
            </div>

            <Dialog header="Tambah tahun pelajaran" visible={tahunPelajaranDialog} style={{ width: "450px" }} footer={tahunPelajaranDialogFooter} onHide={hideTahunPelajaran}>
                <div className="p-fluid">
                    <div className="field grid">
                        <InputText id="tahunPelajaran" placeholder="Contoh '2000/2001'" value={tahunPelajaran} onChange={(e) => setTahunPelajaran((e.target && e.target.value) || "")} required autoFocus className={classNames({ "p-invalid": tahunPelajaran === "" })} />
                        {tahunPelajaran === "" && <small className="p-invalid">Tahun pelajaran tidak boleh kosong.</small>}
                    </div>
                </div>
            </Dialog>

            <Dialog visible={hapusTahunPelajaranDialog} style={{ width: "450px" }} header="Confirm" modal footer={hapusTahunPelajaranDialogFooter} onHide={hideHapusTahunPelajaranDialog}>
                <div className="flex align-items-center justify-content-center">
                    <i className="pi pi-exclamation-triangle mr-3" style={{ fontSize: "2rem" }} />
                    {tahunPelajaran && (
                        <span>
                            Anda yakin akan menghapus tahun pelajaran <b>{tahunPelajaran}</b>?
                        </span>
                    )}
                </div>
            </Dialog>
        </div>
    );
};
