import React, { useState, useEffect } from "react";
import { Dropdown } from "primereact/dropdown";
import { Chart } from "primereact/chart";
import { DataTable } from "primereact/datatable";
import { Column } from "primereact/column";
import { formatRp } from "konversi-rupiah";
import SekolahService from "../service/SekolahService";
import LaporanService from "../service/LaporanService";

export const Dashboard = () => {
    const sekolahService = new SekolahService();
    const [transaksiTerakhir, setTransaksiTerakhir] = useState(null);
    const [uangMasuk, setUangMasuk] = useState(0);
    const [uangMasukBulanIni, setUangMasukBulanIni] = useState(0);
    const [semuaSiswa, setSemuaSiswa] = useState(0);
    const [siswaTahunIni, setSiswaTahunIni] = useState(0);
    const [selectTahun, setSelectTahun] = useState(null);
    const [bar, setBar] = useState([]);

    useEffect(() => {
        const fetchData = async () => {
            const tahunPelajaran = await sekolahService.getTpSekolah();
            const dashboardData = await LaporanService.dashboard(tahunPelajaran.data.data.tahunPelajaranSekarang);
            const barData = await LaporanService.bar(new Date().getFullYear());
            setBar(barData.data.bar);
            setUangMasuk(dashboardData.data.data.semuaTransaksi);
            setUangMasukBulanIni(dashboardData.data.data.transaksiBulanini);
            setSemuaSiswa(dashboardData.data.data.semuaSiswa);
            setSiswaTahunIni(dashboardData.data.data.siswaTahunIni);
            setTransaksiTerakhir(dashboardData.data.data.transaksi);
        };
        fetchData();
    }, []);

    const onTahunChange = async (e) => {
        setSelectTahun(e.value);
        const barData = await LaporanService.bar(e.value.tahun);
        setBar(barData.data.bar);
    };

    const tahun = () => {
        let _tahun = [];
        for (let index = 0; index < 30; index++) {
            const tahunSekarang = { tahun: 2008 + index };
            _tahun.push(tahunSekarang);
        }
        return _tahun;
    };
    const lineData = {
        labels: ["Jan", "Feb", "Mar", "Apr", "Mei", "Jun", "Jul", "Agu", "Sep", "Okt", "Nov", "Des"],
        datasets: [
            {
                label: "Pembayaran Masuk",
                data: bar,
                fill: false,
                backgroundColor: "#2f4860",
                borderColor: "#2f4860",
                tension: 0.4,
            },
        ],
    };

    return (
        <div className="grid">
            <div className="col-12 lg:col-6 xl:col-3">
                <div className="card mb-0">
                    <div className="flex justify-content-between mb-3">
                        <div>
                            <span className="block text-500 font-medium mb-3">Uang Masuk</span>
                            <div className="text-900 font-medium text-xl">{formatRp(uangMasuk)}</div>
                        </div>
                        <div className="flex align-items-center justify-content-center bg-blue-100 border-round" style={{ width: "2.5rem", height: "2.5rem" }}>
                            <i className="pi pi-wallet text-blue-500 text-xl" />
                        </div>
                    </div>
                </div>
            </div>
            <div className="col-12 lg:col-6 xl:col-3">
                <div className="card mb-0">
                    <div className="flex justify-content-between mb-3">
                        <div>
                            <span className="block text-500 font-medium mb-3">Bulan ini</span>
                            <div className="text-900 font-medium text-xl">{formatRp(uangMasukBulanIni)}</div>
                        </div>
                        <div className="flex align-items-center justify-content-center bg-orange-100 border-round" style={{ width: "2.5rem", height: "2.5rem" }}>
                            <i className="pi pi-map-marker text-orange-500 text-xl" />
                        </div>
                    </div>
                </div>
            </div>
            <div className="col-12 lg:col-6 xl:col-3">
                <div className="card mb-0">
                    <div className="flex justify-content-between mb-3">
                        <div>
                            <span className="block text-500 font-medium mb-3">Siswa Tahun Ini</span>
                            <div className="text-900 font-medium text-xl">{siswaTahunIni}</div>
                        </div>
                        <div className="flex align-items-center justify-content-center bg-cyan-100 border-round" style={{ width: "2.5rem", height: "2.5rem" }}>
                            <i className="pi pi-user text-cyan-500 text-xl" />
                        </div>
                    </div>
                </div>
            </div>
            <div className="col-12 lg:col-6 xl:col-3">
                <div className="card mb-0">
                    <div className="flex justify-content-between mb-3">
                        <div>
                            <span className="block text-500 font-medium mb-3">Semua siswa</span>
                            <div className="text-900 font-medium text-xl">{semuaSiswa}</div>
                        </div>
                        <div className="flex align-items-center justify-content-center bg-purple-100 border-round" style={{ width: "2.5rem", height: "2.5rem" }}>
                            <i className="pi pi-users text-purple-500 text-xl" />
                        </div>
                    </div>
                </div>
            </div>

            <div className="col-12 xl:col-6">
                <div className="card">
                    <h5>Transaksi terbaru</h5>
                    <DataTable value={transaksiTerakhir} className="p-datatable-customers" rows={5} paginator>
                        <Column field="namaSiswa" header="Nama" sortable />
                        <Column field="price" header="Jumlah" sortable body={(data) => formatRp(data.bayar)} />
                        <Column field="tanggal" header="Tanggal" />
                    </DataTable>
                </div>
            </div>

            <div className="col-12 xl:col-6">
                <div className="card">
                    <h5>Data Transaksi</h5>
                    <Dropdown value={selectTahun} options={tahun()} onChange={onTahunChange} optionLabel="tahun" placeholder="Pilih tahun" />
                    <Chart type="bar" data={lineData} />
                </div>
            </div>
        </div>
    );
};
