import React, { useState, useEffect, useRef } from "react";
import classNames from "classnames";
import { AutoComplete } from "primereact/autocomplete";
import { ProgressBar } from "primereact/progressbar";
import { DataTable } from "primereact/datatable";
import { Column } from "primereact/column";
import { Button } from "primereact/button";
import { Dialog } from "primereact/dialog";
import { Toast } from "primereact/toast";
import SekolahService from "../service/SekolahService";
import StudentService from "../service/StudentService";
import TransaksiService from "../service/TransaksiService";
import { ClassesService } from "../service/ClassesService";
import { InputText } from "primereact/inputtext";
import { Tag } from "primereact/tag";
import { formatRp } from "konversi-rupiah";
import axios from "axios";

export const Transaction = () => {
    const sekolahService = new SekolahService();
    const classesService = new ClassesService();
    const siswaService = new StudentService();
    const transaksiService = new TransaksiService();
    const emptyAutoValue = [{ _id: "", id: "", nis: "", nisn: "", nama: "", kotaLahir: "", tanggalLahir: "", kelas: { idKelas: "", tahunPelajaran: "", _id: "" } }];

    const [expandedRows, setExpandedRows] = useState([]);
    const [pembayarans, setPembayarans] = useState([]);

    const [autoValue, setAutoValue] = useState(emptyAutoValue);
    const [autoFilteredValue, setAutoFilteredValue] = useState([]);
    const [selectedAutoValue, setSelectedAutoValue] = useState(null);
    const [selectedSiswaValue, setSelectedSiswaValue] = useState(null);
    const [visibleResult, setVisibleResult] = useState(false);

    const [transaksiDialog, setTransaksiDialog] = useState(false);
    const [transaksiSiswaDialog, setTransaksiSiswaDialog] = useState(false);
    const [submitted, setSubmitted] = useState(false);
    const [transaksiPembayaranSiswa, setTransaksiPembayaranSiswa] = useState([]);

    const [subPembayaranSiswas, setSubPembayaranSiswas] = useState([]);
    const [bayarValue, setBayarValue] = useState("");
    const [transaksiValue, setTransaksiValue] = useState(null);

    const [kelass, setKelass] = useState([]);
    const toast = useRef(null);
    const dt = useRef(null);

    useEffect(() => {
        const fetchData = async () => {
            const tahunPelajaran = await sekolahService.getTpSekolah();

            const pembayaran = await transaksiService.lihatPembayaranTahun(tahunPelajaran.data.data.tahunPelajaranSekarang);
            setPembayarans(pembayaran.data.data);

            const _siswa = await siswaService.getStudents();
            setAutoValue(_siswa.data.data);
            const kelass = await classesService.getAllClasses();
            setKelass(kelass.data.data);
        };
        fetchData();
    }, []);

    const clickSiswa = async () => {
        if (selectedAutoValue && typeof selectedAutoValue !== "string") {
            setSelectedSiswaValue(selectedAutoValue);
            return setVisibleResult(true);
        }
        return setVisibleResult(false);
    };

    const exportCSV = (data) => {
        const urls = `/api/export/pembayaran?idPembayaran=${data._id}`;
        axios({
            url: urls,
            method: "GET",
            responseType: "blob", // important
        }).then((response) => {
            const url = window.URL.createObjectURL(new Blob([response.data]));
            const link = document.createElement("a");
            link.href = url;
            link.setAttribute("download", `${data.namaPembayaran}.xlsx`);
            document.body.appendChild(link);
            link.click();
        });
    };
    const setSiswaSelected = (value) => {
        setSelectedAutoValue(value);

        if (selectedAutoValue && typeof selectedAutoValue !== "string") {
            setSelectedSiswaValue(selectedAutoValue);
            return setVisibleResult(true);
        }
        return setVisibleResult(false);
    };

    const cariSiswa = (event) => {
        setTimeout(() => {
            if (!event.query.trim().length) {
                setAutoFilteredValue([...autoValue]);
            } else {
                setAutoFilteredValue(
                    autoValue.filter((siswa) => {
                        return siswa.nama.toLowerCase().startsWith(event.query.toLowerCase());
                    })
                );
            }
        }, 250);
    };

    const fetchPembayaranSiswa = () => {
        try {
            const payload = { tahunPelajaran: selectedAutoValue.tahunPelajaran, idSiswa: selectedSiswaValue.id };
            transaksiService
                .lihatPembayaranSiswa(payload)
                .then((res) => {
                    setTransaksiPembayaranSiswa(res.data.data);
                    setTransaksiDialog(true);
                })
                .catch((error) => {
                    const errResponse = error.response;
                    if (!errResponse.data.message) {
                        toast.current.show({ severity: "error", summary: "Internal Server Error", detail: error.response.statusText, life: 3000 });
                    } else {
                        toast.current.show({ severity: "warn", summary: "Warning", detail: error.response.data.message, life: 3000 });
                    }
                });
        } catch (error) {
            setTransaksiPembayaranSiswa([]);
        }
    };

    const openTransaksi = () => {
        if (selectedSiswaValue) {
            fetchPembayaranSiswa();
        }
    };

    const hideTransaksi = () => {
        setTransaksiDialog(false);
        // console.log(transaksiPembayaranSiswa);
        setTransaksiPembayaranSiswa([]);
    };

    const openTransaksiSiswa = async (payload) => {
        try {
            transaksiService
                .lihatSubPembayaranSiswa(payload)
                .then((res) => {
                    setBayarValue("");
                    setSubPembayaranSiswas(res.data.data);
                    setTransaksiSiswaDialog(true);
                    setTransaksiValue(res.data.payload);
                    toast.current.show({ severity: "success", summary: "Successful", detail: res.data.message, life: 3000 });
                })
                .catch((error) => {
                    const errResponse = error.response;
                    if (!errResponse.data.message) {
                        toast.current.show({ severity: "error", summary: "Internal Server Error", detail: error.response.statusText, life: 3000 });
                    } else {
                        toast.current.show({ severity: "warn", summary: "Warning", detail: error.response.data.message, life: 3000 });
                    }
                });
        } catch (error) {
            setSubPembayaranSiswas([]);
        }
    };

    const hideTransaksiSiswa = () => {
        setSubPembayaranSiswas([]);
        setTransaksiSiswaDialog(false);
    };

    const bayarSekarang = () => {
        try {
            if (bayarValue) {
                setSubmitted(true);
                const payload = { ...transaksiValue, bayar: Number(bayarValue) };
                transaksiService
                    .tambahTransaksi(payload)
                    .then((res) => {
                        fetchPembayaranSiswa();
                        setTransaksiSiswaDialog(false);
                        setSubmitted(false);
                        toast.current.show({ severity: "success", summary: "Successful", detail: res.data.message, life: 3000 });
                    })
                    .catch((error) => {
                        setSubmitted(false);
                        const errResponse = error.response;
                        if (!errResponse.data.message) {
                            toast.current.show({ severity: "error", summary: "Internal Server Error", detail: error.response.statusText, life: 3000 });
                        } else {
                            toast.current.show({ severity: "warn", summary: "Warning", detail: error.response.data.message, life: 3000 });
                        }
                    });
            }
        } catch (error) {}
    };

    const hapusTransaksi = (id) => {
        try {
            transaksiService
                .hapusTransaksi(id)
                .then((res) => {
                    fetchPembayaranSiswa();
                    setTransaksiSiswaDialog(false);
                    toast.current.show({ severity: "success", summary: "Successful", detail: res.data.message, life: 3000 });
                })
                .catch((error) => {
                    const errResponse = error.response;
                    if (!errResponse.data.message) {
                        toast.current.show({ severity: "error", summary: "Internal Server Error", detail: error.response.statusText, life: 3000 });
                    } else {
                        toast.current.show({ severity: "warn", summary: "Warning", detail: error.response.data.message, life: 3000 });
                    }
                });
        } catch (error) {
            toast.current.show({ severity: "error", summary: "Internal Server Error", detail: error.response.statusText, life: 3000 });
        }
    };

    const getNamaKelas = (id) => {
        const kelas = kelass.filter((item) => {
            return item.id === id;
        });
        if (kelas.length) return kelas[0].name;
    };

    const downloadSiswa = (data) => {
        const urls = `/api/export/siswa?idSiswa=${selectedAutoValue.id}&idPembayaran=${data._id}`;
        axios({
            url: urls,
            method: "GET",
            responseType: "blob", // important
        }).then((response) => {
            const url = window.URL.createObjectURL(new Blob([response.data]));
            const link = document.createElement("a");
            link.href = url;
            link.setAttribute("download", `${selectedAutoValue.nama}.xlsx`);
            document.body.appendChild(link);
            link.click();
        });
    };

    const bodyTemplate = (data, props) => {
        return (
            <>
                <span className="p-column-title">{props.header}</span>
                {data[props.field]}
            </>
        );
    };

    const kelasBodyTemplate = (data, props) => {
        const namaKelas = getNamaKelas(data[props.field]);
        return (
            <>
                <span className="p-column-title">{props.header}</span>
                {namaKelas}
            </>
        );
    };

    const actionDownloadTemplate = (data, props) => {
        return (
            <>
                <Button icon="pi pi-download" className="p-button-success" onClick={() => exportCSV(data)} />
                {/* <Button icon="pi pi-print" className="p-button-secondary" onClick={() => editNamaPembayaranDialog(data)} /> */}
            </>
        );
    };

    const actionDownloadSiswaTemplate = (data) => {
        return (
            <>
                <Button icon="pi pi-download" className="p-button-success" onClick={() => downloadSiswa(data)} />
            </>
        );
    };

    const actionHapusTransaksi = (data, props) => {
        return (
            <>
                <Button icon="pi pi-trash" className="p-button-danger" onClick={() => hapusTransaksi(data[props.field])} />
            </>
        );
    };

    const formatTanggal = (data, props) => {
        const tanggalLahir = new Date(data[props.field]);
        return <>{tanggalLahir.toLocaleDateString()}</>;
    };

    const formatRupiah = (data, props) => {
        return <>{formatRp(data[props.field])}</>;
    };
    const formatRupiahMin = (data, props) => {
        return (
            <>
                <Tag severity="danger">{formatRp(data[props.field])}</Tag>
            </>
        );
    };

    const rowExpansionTemplate = (data) => {
        return (
            <div className="orders-subtable">
                <DataTable value={data.transaksiSiswa}>
                    <Column field="nama" header="Sub Pembayaran" body={bodyTemplate}></Column>
                    <Column field="dibayar" header="Dibayar" body={formatRupiah}></Column>
                    <Column field="total" header="Total" body={formatRupiah}></Column>
                    <Column field="kekurangan" header="Kekurangan" body={formatRupiahMin}></Column>
                    <Column headerStyle={{ width: "4rem" }} body={(data) => <Button icon="pi pi-wallet" onClick={() => openTransaksiSiswa(data)} className="p-button-danger" />}></Column>
                </DataTable>
            </div>
        );
    };

    const SiswaBodyTemplate = (props) => {
        const tanggalLahir = new Date(props.field.tanggalLahir);
        return (
            <React.Fragment>
                <div className="field grid ml-2">
                    <label className="col-2">NIS</label>
                    <div className="col-10">: {props.field.nis}</div>
                </div>
                <div className="field grid ml-2">
                    <label className="col-2">Nama</label>
                    <div className="col-10">: {props.field.nama}</div>
                </div>
                <div className="field grid ml-2">
                    <label className="col-2">TTL</label>
                    <div className="col-10">: {`${props.field.kotaLahir}, ${tanggalLahir.toLocaleDateString()}`}</div>
                </div>

                {/* <div className="flex align-items-center ml-2 mb-2">
                    <i className="pi pi-history mr-2" />
                    <span>Transaksi terakhir</span>
                </div> */}

                {/* <div className="field grid ml-2">
                    <Tag className="mr-2" severity="warning" value={"Adminstrasi sekolah: 17/09/1996"}></Tag>
                </div> */}

                <Button label="Transaksi" onClick={() => openTransaksi()} className="mb-2" />
            </React.Fragment>
        );
    };

    return (
        <div className="grid table-demo">
            <Toast ref={toast} />
            <div className="md:col-4">
                <div className="card">
                    <div className="p-col-12 p-md-4">
                        <div className="p-inputgroup mb-2">
                            <AutoComplete placeholder="Search" id="dd" value={selectedAutoValue} onChange={(e) => setSiswaSelected(e.value)} suggestions={autoFilteredValue} completeMethod={cariSiswa} field="nama" />
                            <Button icon="pi pi-search" className="p-button" onClick={() => clickSiswa()} />
                        </div>
                        {!visibleResult ? <label className="mt-2">Data tidak ditemukan</label> : <SiswaBodyTemplate field={selectedSiswaValue} />}
                    </div>
                </div>
            </div>
            <div className="md:col-8">
                <div className="card">
                    <h5>Data Transaksi</h5>
                    <DataTable value={pembayarans} ref={dt} dataKey="id" className="p-datatable-customers p-text-center" rows={5} paginator>
                        <Column field="namaPembayaran" header="Nama Pembayaran" sortable body={bodyTemplate}></Column>
                        <Column field="idKelas" header="Kelas" body={kelasBodyTemplate}></Column>
                        <Column field="tahunPelajaran" header="Tahun Pelajaran" body={bodyTemplate}></Column>
                        <Column field="tanggalPembuatan" header="Tanggal Pembuatan" body={formatTanggal}></Column>
                        <Column field="_id" header="Download" body={actionDownloadTemplate}></Column>
                    </DataTable>
                </div>
            </div>
            <Dialog visible={transaksiDialog} header="Transaksi" maximizable modal style={{ width: "50vw" }} onHide={hideTransaksi}>
                <DataTable value={transaksiPembayaranSiswa} expandedRows={expandedRows} rows={5} paginator className="p-datatable-customers" dataKey="_id" onRowToggle={(e) => setExpandedRows(e.data)} rowExpansionTemplate={rowExpansionTemplate}>
                    <Column expander headerStyle={{ width: "3rem" }} />
                    <Column field="namaPembayaran" header="Nama Pembayaran" body={bodyTemplate}></Column>
                    <Column field="keterangan" header="Keterangan" body={bodyTemplate}></Column>
                    <Column field="action" header="Aksi" body={actionDownloadSiswaTemplate}></Column>
                </DataTable>
            </Dialog>

            <Dialog visible={transaksiSiswaDialog} header="Detail Transaksi" style={{ width: "35vw" }} onHide={hideTransaksiSiswa}>
                <DataTable value={subPembayaranSiswas} className="p-datatable-customers p-text-center mb-2" rows={5} paginator>
                    <Column field="tanggalBayar" header="Tanggal" body={formatTanggal}></Column>
                    <Column field="bayar" header="Jumlah" body={formatRupiah}></Column>
                    <Column field="_id" header="Hapus" body={actionHapusTransaksi}></Column>
                </DataTable>
                {submitted ? (
                    <ProgressBar mode="indeterminate" className="mb-2" />
                ) : (
                    <div className="p-col-12 p-md-4">
                        <div className="p-inputgroup">
                            <InputText id="tahunPelajaran" type="number" placeholder="Masukan nominal pembayaran" value={bayarValue} onChange={(e) => setBayarValue((e.target && e.target.value) || "")} required autoFocus className={classNames({ "p-invalid": bayarValue === "" })} />
                            <Button label="Bayar Sekarang" onClick={bayarSekarang} />
                        </div>
                        {bayarValue === "" && <small className="p-invalid">Kolom tidak boleh kosong.</small>}
                    </div>
                )}
            </Dialog>
        </div>
    );
};
