import axios from "axios";

export default class SekolahService {
    getSekolah() {
        return axios.get("/api/sekolah");
    }
    getTpSekolah() {
        return axios.get("/api/sekolah/tp");
    }
    putSekolah(sekolah) {
        return axios({
            method: "put",
            url: "/api/sekolah",
            data: sekolah,
        });
    }
    putTpSekolah(sekolah) {
        return axios({
            method: "put",
            url: "/api/sekolah/tp",
            data: sekolah,
        });
    }
    postTpSekolah(sekolah) {
        return axios({
            method: "post",
            url: "/api/sekolah/tp",
            data: sekolah,
        });
    }
    deleteTpSekolah(sekolah) {
        return axios({
            method: "delete",
            url: "/api/sekolah/tp",
            data: sekolah,
        });
    }
}
