import React, { useState, useEffect, useRef } from "react";
import classNames from "classnames";
import { DataTable } from "primereact/datatable";
import { Column } from "primereact/column";
import { Button } from "primereact/button";
import { Dialog } from "primereact/dialog";
import { Toast } from "primereact/toast";
import { InputText } from "primereact/inputtext";
import { Dropdown } from "primereact/dropdown";
import PembayaranService from "../service/PembayaranService";
import SekolahService from "../service/SekolahService";
import { ClassesService } from "../service/ClassesService";

export const Payment = () => {
    const pembayaranService = new PembayaranService();
    const sekolahService = new SekolahService();
    const classesService = new ClassesService();
    const pembayaranEmpty = {
        _id: null,
        namaPembayaran: "",
        tahunPelajaran: "",
        idKelas: "",
        tanggalPembuatan: null,
        subBayar: [],
        transaksi: [],
        __v: null,
    };
    const subPembayaranEmpty = { namaSub: "", total: "" };
    const [refreshData, setRefreshData] = useState(false);
    const [expandedRows, setExpandedRows] = useState([]);
    const [pembayaran, setPembayaran] = useState(pembayaranEmpty);
    const [pembayarans, setPembayarans] = useState([]);
    const [pembayaranReguler, setPembayaranReguler] = useState([]);
    const [subPembayaran, setSubPembayaran] = useState(subPembayaranEmpty);

    const [hapusPembayaranDialog, setHapusPembayaranDialog] = useState(false);
    const [hapusSubPembayaranDialog, sethapusSubPembayaranDialog] = useState(false);
    const [tambahPembayaranDialog, setTambahPembayaranDialog] = useState(false);
    const [tambahPembayaranRegulerDialog, setTambahPembayaranRegulerDialog] = useState(false);
    const [tambahSubPembayaranDialog, setTambahSubPembayaranDialog] = useState(false);

    const [tahunPelajaran, setTahunPelajaran] = useState(null);
    const [tahunPelajarans, setTahunPelajarans] = useState([]);
    const [kelas, setKelas] = useState(null);
    const [kelass, setKelass] = useState([]);
    const toast = useRef(null);

    useEffect(() => {
        const fetchData = async () => {
            const pembayaran = await pembayaranService.lihatSemuaPembayaran();
            filterPembayaran(pembayaran.data.data);

            const tahunPelajaran = await sekolahService.getTpSekolah();
            setTahunPelajarans(tahunPelajaran.data.data.tahunPelajaran);

            const kelass = await classesService.getAllClasses();
            setKelass(kelass.data.data);
        };
        fetchData();
    }, [refreshData]);

    const filterPembayaran = (data) => {
        const _pembayaranReguler = data.filter((item) => {
            return item.idKelas === "all";
        });
        const _prmbayarans = data.filter((item) => {
            return item.idKelas !== "all";
        });
        setPembayaranReguler(_pembayaranReguler);
        setPembayarans(_prmbayarans);
    };

    const editNamaPembayaranDialog = (data) => {
        setPembayaran(data);
        setTambahPembayaranDialog(true);
        const kelasLocal = getKelas(data.idKelas);
        setKelas(kelasLocal);
        setTahunPelajaran({ tahunPelajaran: data.tahunPelajaran });
    };

    const konfirmasiHapusPembayaran = (value) => {
        setPembayaran(value);
        setHapusPembayaranDialog(true);
    };

    const konfirmasiHapusSubPembayaran = (value) => {
        setSubPembayaran(value);
        sethapusSubPembayaranDialog(true);
    };

    const getKelas = (id) => {
        const kelas = kelass.filter((item) => {
            return item.id === id;
        });
        if (kelas.length) return kelas[0];
    };

    const getNamaKelas = (id) => {
        const kelas = kelass.filter((item) => {
            return item.id === id;
        });
        if (kelas.length) return kelas[0].name;
    };

    const hapusPembayaran = () => {
        pembayaranService
            .hapusPembayaran(pembayaran._id)
            .then((res) => {
                setRefreshData(true);
                hideHapusPembayaranDialog();
                toast.current.show({ severity: "success", summary: "Successful", detail: res.data.message, life: 3000 });
            })
            .catch((error) => {
                const errResponse = error.response;
                if (!errResponse.data.message) {
                    toast.current.show({ severity: "error", summary: "Internal Server Error", detail: error.response.statusText, life: 3000 });
                } else {
                    toast.current.show({ severity: "warn", summary: "Warning", detail: error.response.data.message, life: 3000 });
                }
            });
    };

    const hapusSubPembayaran = () => {
        pembayaranService
            .hapusSubPembayaran(subPembayaran._id)
            .then((res) => {
                setRefreshData(true);
                hideHapusSubPembayaranDialog();
                toast.current.show({ severity: "success", summary: "Successful", detail: res.data.message, life: 3000 });
            })
            .catch((error) => {
                const errResponse = error.response;
                if (!errResponse.data.message) {
                    toast.current.show({ severity: "error", summary: "Internal Server Error", detail: error.response.statusText, life: 3000 });
                } else {
                    toast.current.show({ severity: "warn", summary: "Warning", detail: error.response.data.message, life: 3000 });
                }
            });
    };

    const openTambahPembayaran = () => {
        setTambahPembayaranDialog(true);
    };

    const openTambahPembayaranReguler = () => {
        let _pembayaran = { ...pembayaran };
        _pembayaran.idKelas = "all";
        setPembayaran(_pembayaran);
        setTambahPembayaranRegulerDialog(true);
    };

    const openTambahSubPembayaran = (idPembayaran) => {
        let _subPembayaran = { ...subPembayaran };
        _subPembayaran.idPembayaran = idPembayaran;
        setSubPembayaran(_subPembayaran);
        setTambahSubPembayaranDialog(true);
    };

    const onInputChange = (e, name) => {
        const val = (e.target && e.target.value) || "";
        let _pembayaran = { ...pembayaran };
        _pembayaran[`${name}`] = val;
        setPembayaran(_pembayaran);
    };

    const onInputChangeSub = (e, name) => {
        const val = (e.target && e.target.value) || "";
        let _subPembayaran = { ...subPembayaran };
        _subPembayaran[`${name}`] = val;
        setSubPembayaran(_subPembayaran);
    };

    const onChangeTahunPelajaran = (e) => {
        const val = (e.target && e.target.value) || "";
        let _pembayaran = { ...pembayaran };
        _pembayaran.tahunPelajaran = val.tahunPelajaran;
        setPembayaran(_pembayaran);
        setTahunPelajaran(val);
    };

    const onChangeKelas = (e) => {
        const val = (e.target && e.target.value) || "";
        let _pembayaran = { ...pembayaran };
        _pembayaran.idKelas = val.id;
        setPembayaran(_pembayaran);
        setKelas(val);
    };

    const simpanPembayaran = async () => {
        if (pembayaran.namaPembayaran && pembayaran.idKelas && pembayaran.tahunPelajaran) {
            if (!pembayaran._id) {
                return pembayaranService
                    .tambahPembayaran(pembayaran)
                    .then((res) => {
                        setRefreshData(true);
                        hideTambahPembayaranDialog();
                        toast.current.show({ severity: "success", summary: "Successful", detail: res.data.message, life: 3000 });
                    })
                    .catch((error) => {
                        const errResponse = error.response;
                        if (!errResponse.data.message) {
                            toast.current.show({ severity: "error", summary: "Internal Server Error", detail: error.response.statusText, life: 3000 });
                        } else {
                            toast.current.show({ severity: "warn", summary: "Warning", detail: error.response.data.message, life: 3000 });
                        }
                    });
            }
            return pembayaranService
                .editPembayaran(pembayaran)
                .then((res) => {
                    setRefreshData(true);
                    hideTambahPembayaranDialog();
                    toast.current.show({ severity: "success", summary: "Successful", detail: res.data.message, life: 3000 });
                })
                .catch((error) => {
                    const errResponse = error.response;
                    if (!errResponse.data.message) {
                        toast.current.show({ severity: "error", summary: "Internal Server Error", detail: error.response.statusText, life: 3000 });
                    } else {
                        toast.current.show({ severity: "warn", summary: "Warning", detail: error.response.data.message, life: 3000 });
                    }
                });
        }
    };

    const tambahSubPembayaran = () => {
        if (subPembayaran.namaSub && subPembayaran.total && subPembayaran.idPembayaran) {
            pembayaranService
                .tambahSubPembayaran(subPembayaran)
                .then((res) => {
                    setRefreshData(true);
                    hideTambahSubPembayaranDialog();
                    toast.current.show({ severity: "success", summary: "Successful", detail: res.data.message, life: 3000 });
                })
                .catch((error) => {
                    const errResponse = error.response;
                    if (!errResponse.data.message) {
                        toast.current.show({ severity: "error", summary: "Internal Server Error", detail: error.response.statusText, life: 3000 });
                    } else {
                        toast.current.show({ severity: "warn", summary: "Warning", detail: error.response.data.message, life: 3000 });
                    }
                });
        }
    };

    const hideHapusPembayaranDialog = () => {
        setPembayaran(pembayaranEmpty);
        setHapusPembayaranDialog(false);
        setRefreshData(false);
    };

    const hideHapusSubPembayaranDialog = () => {
        setSubPembayaran(subPembayaranEmpty);
        sethapusSubPembayaranDialog(false);
        setRefreshData(false);
    };

    const hideTambahPembayaranDialog = () => {
        setTambahPembayaranDialog(false);
        setTambahPembayaranRegulerDialog(false);
        setRefreshData(false);
        setTahunPelajaran(null);
        setKelas(null);
        setPembayaran(pembayaranEmpty);
    };

    const hideTambahSubPembayaranDialog = () => {
        setTambahSubPembayaranDialog(false);
        setRefreshData(false);
        setTahunPelajaran(null);
        setKelas(null);
        setPembayaran(pembayaranEmpty);
        setSubPembayaran(subPembayaranEmpty);
    };

    const pembayaranTableHeader = (
        <div className="table-header-container">
            <Button icon="pi pi-plus" label="Tambah" onClick={() => openTambahPembayaran()} className="mr-2" />
        </div>
    );

    const pembayaranRegulerTableHeader = (
        <div className="table-header-container">
            <Button icon="pi pi-plus" label="Tambah" onClick={() => openTambahPembayaranReguler()} className="mr-2" />
        </div>
    );

    const bodyTemplate = (data, props) => {
        return (
            <>
                <span className="p-column-title">{props.header}</span>
                {data[props.field]}
            </>
        );
    };

    const kelasBodyTemplate = (data, props) => {
        const namaKelas = getNamaKelas(data[props.field]);
        return (
            <>
                <span className="p-column-title">{props.header}</span>
                {namaKelas}
            </>
        );
    };

    const actionBodyTemplate = (data) => {
        return (
            <>
                <Button icon="pi pi-pencil" className="p-button-rounded mr-2" onClick={() => editNamaPembayaranDialog(data)} />
                <Button icon="pi pi-trash" className="p-button-rounded p-button-danger" onClick={() => konfirmasiHapusPembayaran(data)} />
            </>
        );
    };

    const hapusPembayaranDialogFooter = () => {
        return (
            <div>
                <Button label="Batal" icon="pi pi-times" onClick={hideHapusPembayaranDialog} />
                <Button label="Hapus" icon="pi pi-check" onClick={() => hapusPembayaran()} className="p-button-text" />
            </div>
        );
    };

    const hapusSubPembayaranDialogFooter = () => {
        return (
            <div>
                <Button label="Batal" icon="pi pi-times" onClick={hideHapusSubPembayaranDialog} />
                <Button label="Hapus" icon="pi pi-check" onClick={() => hapusSubPembayaran()} className="p-button-text" />
            </div>
        );
    };

    const tambahPembayaranDialogFooter = () => {
        return (
            <div>
                <Button label="Batal" icon="pi pi-times" onClick={hideTambahPembayaranDialog} />
                <Button label="Simpan" icon="pi pi-check" onClick={() => simpanPembayaran()} className="p-button-text" />
            </div>
        );
    };

    const tambahSubPembayaranDialogFooter = () => {
        return (
            <div>
                <Button label="Batal" icon="pi pi-times" onClick={hideTambahSubPembayaranDialog} />
                <Button label="Simpan" icon="pi pi-check" onClick={() => tambahSubPembayaran()} className="p-button-text" />
            </div>
        );
    };

    const rowExpansionTemplate = (data) => {
        return (
            <div className="orders-subtable">
                <Button label="Tambah" icon="pi pi-plus" onClick={() => openTambahSubPembayaran(data._id)} className="mb-2 p-button-success" />
                <DataTable value={data.subBayar}>
                    <Column field="namaSub" header="Sub Pembayaran" sortable body={bodyTemplate}></Column>
                    <Column field="total" header="Total" sortable body={bodyTemplate}></Column>
                    <Column headerStyle={{ width: "4rem" }} body={(data) => <Button icon="pi pi-trash" onClick={() => konfirmasiHapusSubPembayaran(data)} className="p-button-danger" />}></Column>
                </DataTable>
            </div>
        );
    };
    return (
        <div className="grid table-demo">
            <div className="col-12">
                <div className="card">
                    <h5>Pembayaran Reguler</h5>

                    <Toast ref={toast} />
                    <DataTable value={pembayaranReguler} expandedRows={expandedRows} rows={5} paginator className="p-datatable-customers" dataKey="_id" onRowToggle={(e) => setExpandedRows(e.data)} rowExpansionTemplate={rowExpansionTemplate} header={pembayaranRegulerTableHeader}>
                        <Column expander headerStyle={{ width: "3rem" }} />
                        <Column field="namaPembayaran" header="Nama Pembayaran" sortable body={bodyTemplate}></Column>
                        <Column field="tahunPelajaran" header="Tahun pelajaran" sortable body={bodyTemplate}></Column>
                        <Column field="action" header="Aksi" sortable body={actionBodyTemplate}></Column>
                    </DataTable>
                </div>
            </div>
            <div className="col-12">
                <div className="card">
                    <h5>Pembayaran Tahunan</h5>

                    <Toast ref={toast} />
                    <DataTable value={pembayarans} expandedRows={expandedRows} rows={5} paginator className="p-datatable-customers" dataKey="_id" onRowToggle={(e) => setExpandedRows(e.data)} rowExpansionTemplate={rowExpansionTemplate} header={pembayaranTableHeader}>
                        <Column expander headerStyle={{ width: "3rem" }} />
                        <Column field="namaPembayaran" header="Nama Pembayaran" sortable body={bodyTemplate}></Column>
                        <Column field="idKelas" header="Kelas" sortable body={kelasBodyTemplate}></Column>
                        <Column field="tahunPelajaran" header="Tahun pelajaran" sortable body={bodyTemplate}></Column>
                        <Column field="action" header="Aksi" sortable body={actionBodyTemplate}></Column>
                    </DataTable>
                </div>
            </div>

            <Dialog header="Konfirmasi" visible={hapusPembayaranDialog} style={{ width: "450px" }} footer={hapusPembayaranDialogFooter} onHide={hideHapusPembayaranDialog}>
                <div className="flex align-items-center justify-content-center">
                    <i className="pi pi-exclamation-triangle mr-3" style={{ fontSize: "2rem" }} />
                    <span>
                        Anda yakin akan menghapus pembayaran <b>{pembayaran.namaPembayaran}</b>?
                    </span>
                </div>
            </Dialog>

            <Dialog header="Konfirmasi" visible={hapusSubPembayaranDialog} style={{ width: "450px" }} footer={hapusSubPembayaranDialogFooter} onHide={hideHapusSubPembayaranDialog}>
                <div className="flex align-items-center justify-content-center">
                    <i className="pi pi-exclamation-triangle mr-3" style={{ fontSize: "2rem" }} />
                    <span>
                        Anda yakin akan menghapus sub pembayaran <b>{subPembayaran.namaSub}</b>?
                    </span>
                </div>
            </Dialog>

            <Dialog visible={tambahPembayaranRegulerDialog} style={{ width: "450px" }} header="Detail Pembayaran" modal className="p-fluid" footer={tambahPembayaranDialogFooter} onHide={hideTambahPembayaranDialog}>
                <div className="field">
                    <label htmlFor="namaPembayaran">Nama Pembayaran</label>
                    <InputText id="namaPembayaran" value={pembayaran.namaPembayaran} required autoFocus onChange={(e) => onInputChange(e, "namaPembayaran")} className={classNames({ "p-invalid": !pembayaran.namaPembayaran })} />
                    {!pembayaran.namaPembayaran && <small className="p-invalid">Nama pembayaran tidak boleh kosong.</small>}
                </div>
                <div className="field">
                    <label htmlFor="tahunPelajaran">Tahun pelajaran</label>
                    <Dropdown value={tahunPelajaran} onChange={(e) => onChangeTahunPelajaran(e)} options={tahunPelajarans} optionLabel="tahunPelajaran" placeholder="Pilih salha satu" required className={classNames({ "p-invalid": !pembayaran.tahunPelajaran })} />
                    {!pembayaran.tahunPelajaran && <small className="p-invalid">Tahun pelajaran tidak boleh kosong.</small>}
                </div>
            </Dialog>

            <Dialog visible={tambahPembayaranDialog} style={{ width: "450px" }} header="Detail Pembayaran" modal className="p-fluid" footer={tambahPembayaranDialogFooter} onHide={hideTambahPembayaranDialog}>
                <div className="field">
                    <label htmlFor="namaPembayaran">Nama Pembayaran</label>
                    <InputText id="namaPembayaran" value={pembayaran.namaPembayaran} required autoFocus onChange={(e) => onInputChange(e, "namaPembayaran")} className={classNames({ "p-invalid": !pembayaran.namaPembayaran })} />
                    {!pembayaran.namaPembayaran && <small className="p-invalid">Nama pembayaran tidak boleh kosong.</small>}
                </div>
                <div className="field">
                    <label htmlFor="tahunPelajaran">Kelas</label>
                    <Dropdown value={kelas} onChange={(e) => onChangeKelas(e)} options={kelass} optionLabel="name" placeholder="Pilih salha satu" required className={classNames({ "p-invalid": !pembayaran.idKelas })} />
                    {!pembayaran.idKelas && <small className="p-invalid">Kelas tidak boleh kosong.</small>}
                </div>
                <div className="field">
                    <label htmlFor="tahunPelajaran">Tahun pelajaran</label>
                    <Dropdown value={tahunPelajaran} onChange={(e) => onChangeTahunPelajaran(e)} options={tahunPelajarans} optionLabel="tahunPelajaran" placeholder="Pilih salha satu" required className={classNames({ "p-invalid": !pembayaran.tahunPelajaran })} />
                    {!pembayaran.tahunPelajaran && <small className="p-invalid">Kelas tidak boleh kosong.</small>}
                </div>
            </Dialog>

            <Dialog visible={tambahSubPembayaranDialog} style={{ width: "450px" }} header="Tambah sub pembayaran" modal className="p-fluid" footer={tambahSubPembayaranDialogFooter} onHide={hideTambahSubPembayaranDialog}>
                <div className="field">
                    <label htmlFor="namaSub">Nama Sub Pembayaran</label>
                    <InputText id="namaSub" value={subPembayaran.namaSub} required autoFocus onChange={(e) => onInputChangeSub(e, "namaSub")} className={classNames({ "p-invalid": !subPembayaran.namaSub })} />
                    {!subPembayaran.namaSub && <small className="p-invalid">Nama sub pembayaran tidak boleh kosong.</small>}
                </div>
                <div className="field">
                    <label htmlFor="total">Total</label>
                    <InputText id="total" type="number" value={subPembayaran.total} required onChange={(e) => onInputChangeSub(e, "total")} className={classNames({ "p-invalid": !subPembayaran.total })} />
                    {!subPembayaran.total && <small className="p-invalid">Total sub pembayaran tidak boleh kosong dan format harus angka/nomor.</small>}
                </div>
            </Dialog>
        </div>
    );
};
