export const formatDate = (date) => {
    const newDate = new Date(date);
    let year = newDate.getFullYear();
    let month = newDate.getMonth() + 1;
    let dt = newDate.getDate();

    if (dt < 10) {
        dt = "0" + dt;
    }
    if (month < 10) {
        month = "0" + month;
    }

    return year + "-" + month + "-" + dt;
};

export const handleDates = (list, prop) => {
    return list.map((item) => {
        const obj = Object.assign({}, item);
        obj[prop] = new Date(obj[prop]).toLocaleDateString();
        return obj;
    });
};

export const filterSiswa = (list, tahunPelajaran) => {
    return list.map((item) => {
        const arrKelas = item.kelas;
        const filterKelas = arrKelas.filter((x) => {
            return x.tahunPelajaran === tahunPelajaran;
        });
        if (filterKelas.length) {
            item.kelas = filterKelas[0];
            return item;
        }

        item.kelas = { idKelas: "", tahunPelajaran: "" };
        return item;
    });
};
