import axios from "axios";

export class ClassesService {
    postClasses(newClass) {
        return axios({
            method: "post",
            url: "/api/classes",
            data: newClass,
        });
    }

    getAllClasses() {
        return axios.get("/api/classes");
    }

    deleteClasses(id) {
        return axios.delete("/api/classes/" + id);
    }

    getMajorById(code) {
        return axios.get("/api/major/" + code);
    }

    getAllMajor() {
        return axios.get("/api/major");
    }

    postMajor(newMajor) {
        return axios({
            method: "post",
            url: "/api/major",
            data: newMajor,
        });
    }

    putClasses(classes) {
        return axios({
            method: "put",
            url: "/api/classes",
            data: classes,
        });
    }

    deleteMajor(code) {
        return axios.delete("/api/major/" + code);
    }
}
