import React, { useState, useEffect, useRef } from "react";
import classNames from "classnames";
import { DataTable } from "primereact/datatable";
import { Calendar } from "primereact/calendar";
import { Column } from "primereact/column";
import { Toast } from "primereact/toast";
import { Button } from "primereact/button";
import { FileUpload } from "primereact/fileupload";
import { Toolbar } from "primereact/toolbar";
import { Dialog } from "primereact/dialog";
import { Dropdown } from "primereact/dropdown";
import { InputText } from "primereact/inputtext";
import StudentService from "../service/StudentService";
import SekolahService from "../service/SekolahService";
import { handleDates } from "../utils/formatDate";
import { ProgressBar } from "primereact/progressbar";

export const Student = () => {
    const emptyStudent = {
        id: null,
        nisn: null,
        nis: null,
        nama: "",
        kotaLahir: "",
        tanggalLahir: null,
        tahunPelajaran: null,
    };
    const [totalSize, setTotalSize] = useState(false);
    const [student, setStudent] = useState(emptyStudent);
    const [students, setStudents] = useState(null);
    const [studentDialog, setStudentDialog] = useState(false);
    const [deleteStudentDialog, setDeleteStudentDialog] = useState(false);
    const [deleteStudentsDialog, setDeleteStudentsDialog] = useState(false);
    const [selectedStudents, setSelectedStudents] = useState(null);

    const [submitted, setSubmitted] = useState(false);
    const [globalFilter, setGlobalFilter] = useState(null);
    const toast = useRef(null);
    const dt = useRef(null);
    const [tahunPelajaranItem, setTahunPelajaranItem] = useState([]);

    useEffect(() => {
        const studentService = new StudentService();
        const sekolahService = new SekolahService();

        const fetchStudents = async () => {
            const getStudents = await studentService.getStudents();
            const studentsData = handleDates(getStudents.data.data, "tanggalLahir");
            setStudents(studentsData);

            const getTpSekolah = await sekolahService.getTpSekolah();
            setTahunPelajaranItem(getTpSekolah.data.data.tahunPelajaran);
        };
        fetchStudents();
    }, [student, totalSize]);

    const openStudent = () => {
        setStudent(emptyStudent);
        setSubmitted(false);
        setStudentDialog(true);
    };

    const hideStudent = () => {
        setSubmitted(false);
        setStudentDialog(false);
    };

    const hideDeleteStudentDialog = () => {
        setDeleteStudentDialog(false);
    };

    const hideDeleteStudentsDialog = () => {
        setDeleteStudentsDialog(false);
    };

    const editStudent = (student) => {
        setStudent({ ...student });
        setStudentDialog(true);
    };

    const onChangeTp = (tp) => {
        let _student = { ...student };
        _student.tahunPelajaran = tp.tahunPelajaran;

        setStudent(_student);
    };

    const saveStudent = () => {
        setSubmitted(true);
        const editStudent = new StudentService();
        if (student.id) {
            editStudent
                .putStudents(student)
                .then((res) => {
                    toast.current.show({ severity: "success", summary: "Successful", detail: res.data.message, life: 3000 });
                    setStudent(emptyStudent);
                    hideStudent();
                })
                .catch((error) => {
                    const errResponse = error.response;
                    if (!errResponse.data.message) {
                        toast.current.show({ severity: "error", summary: "Internal Server Error", detail: error.response.statusText, life: 3000 });
                    } else {
                        toast.current.show({ severity: "warn", summary: "Warning", detail: error.response.data.message, life: 3000 });
                    }
                });
        } else {
            editStudent
                .postStudents(student)
                .then((res) => {
                    toast.current.show({ severity: "success", summary: "Successful", detail: res.data.message, life: 3000 });
                    setStudent(emptyStudent);
                    hideStudent();
                })
                .catch((error) => {
                    const errResponse = error.response;
                    if (!errResponse.data.message) {
                        toast.current.show({ severity: "error", summary: "Internal Server Error", detail: error.response.statusText, life: 3000 });
                    } else {
                        toast.current.show({ severity: "warn", summary: "Warning", detail: error.response.data.message, life: 3000 });
                    }
                });
        }
    };

    const confirmDeleteStudent = (student) => {
        setStudent(student);
        setDeleteStudentDialog(true);
    };

    const deleteStudent = () => {
        const deleteStudent = new StudentService();
        deleteStudent
            .deleteStudent(student.id)
            .then((res) => {
                setStudent(emptyStudent);
                setDeleteStudentDialog(false);
                toast.current.show({ severity: "success", summary: "Successful", detail: res.data.message, life: 3000 });
                hideDeleteStudentDialog();
            })
            .catch((error) => {
                const errResponse = error.response;
                if (!errResponse.data.message) {
                    toast.current.show({ severity: "error", summary: "Internal Server Error", detail: error.response.statusText, life: 3000 });
                } else {
                    toast.current.show({ severity: "warn", summary: "Warning", detail: error.response.data.message, life: 3000 });
                }
            });
    };

    const exportCSV = () => {
        dt.current.exportCSV();
    };

    const confirmDeleteSelected = () => {
        setDeleteStudentsDialog(true);
    };

    const deleteSelectedStudents = () => {
        const deleteStudent = new StudentService();
        deleteStudent
            .deleteStudents(...selectedStudents)
            .then((res) => {
                setStudent(emptyStudent);
                setSelectedStudents(null);
                hideDeleteStudentsDialog();
                toast.current.show({ severity: "success", summary: "Successful", detail: res.data.message, life: 3000 });
            })
            .catch((error) => {
                const errResponse = error.response;
                if (!errResponse.data.message) {
                    toast.current.show({ severity: "warn", summary: "Warning", detail: error.response.statusText, life: 3000 });
                } else {
                    toast.current.show({ severity: "error", summary: "Internal Server Error", detail: error.response.data.message, life: 3000 });
                }
            });
    };

    const onInputChange = (e, name) => {
        const val = (e.target && e.target.value) || "";
        let _student = { ...student };
        _student[`${name}`] = val;

        setStudent(_student);
    };

    const leftToolbarTemplate = () => {
        return (
            <React.Fragment>
                <div className="my-2">
                    <Button label="New" icon="pi pi-plus" className="p-button-success mr-2" onClick={openStudent} />
                    <Button label="Delete" icon="pi pi-trash" className="p-button-danger" onClick={confirmDeleteSelected} disabled={!selectedStudents || !selectedStudents.length} />
                </div>
            </React.Fragment>
        );
    };

    const rightToolbarTemplate = () => {
        return (
            <React.Fragment>
                <FileUpload
                    name="siswa"
                    mode="basic"
                    label="Import"
                    chooseLabel="Import"
                    className="mr-2 inline-block"
                    url="/api/siswa/upload"
                    onUpload={() => toast.current.show({ severity: "info", summary: "Sukses", detail: "File berhasil upload" })}
                    onError={() => toast.current.show({ severity: "warn", summary: "Gagal", detail: "File gagal upload" })}
                    onProgress={() => setTotalSize(true)}
                    onClear={() => setTotalSize(false)}
                />
                <Button label="Export" icon="pi pi-download" className="p-button-help" onClick={exportCSV} />
            </React.Fragment>
        );
    };

    const namaBodyTemplate = (rowData) => {
        return (
            <>
                <span className="p-column-title">Nama</span>
                {rowData.nama}
            </>
        );
    };

    const nisnBodyTemplate = (rowData) => {
        return (
            <>
                <span className="p-column-title">NISN</span>
                {rowData.nisn}
            </>
        );
    };

    const nisBodyTemplate = (rowData) => {
        return (
            <>
                <span className="p-column-title">NIS</span>
                {rowData.nis}
            </>
        );
    };

    const tanggalLahirBodyTemplate = (rowData) => {
        return (
            <>
                <span className="p-column-title">NIS</span>
                {rowData.tanggalLahir}
            </>
        );
    };

    const kotaLahirBodyTemplate = (rowData) => {
        return (
            <>
                <span className="p-column-title">NIS</span>
                {rowData.kotaLahir}
            </>
        );
    };

    const actionBodyTemplate = (rowData) => {
        return (
            <div className="actions">
                <Button icon="pi pi-pencil" className="p-button-rounded p-button-success mr-2" onClick={() => editStudent(rowData)} />
                <Button icon="pi pi-trash" className="p-button-rounded p-button-warning" onClick={() => confirmDeleteStudent(rowData)} />
            </div>
        );
    };

    const header = (
        <div className="flex flex-column md:flex-row md:justify-content-between md:align-items-center">
            <h5 className="m-0">Data Siswa</h5>
            <span className="block mt-2 md:mt-0 p-input-icon-left">
                <i className="pi pi-search" />
                <InputText type="search" onInput={(e) => setGlobalFilter(e.target.value)} placeholder="Search..." />
            </span>
        </div>
    );

    const studentDialogFooter = (
        <>
            <Button label="Cancel" icon="pi pi-times" className="p-button-text" onClick={hideStudent} />
            <Button label="Save" icon="pi pi-check" className="p-button-text" onClick={saveStudent} />
        </>
    );
    const deleteStudentDialogFooter = (
        <>
            <Button label="No" icon="pi pi-times" className="p-button-text" onClick={hideDeleteStudentDialog} />
            <Button label="Yes" icon="pi pi-check" className="p-button-text" onClick={deleteStudent} />
        </>
    );
    const deleteStudentsDialogFooter = (
        <>
            <Button label="No" icon="pi pi-times" className="p-button-text" onClick={hideDeleteStudentsDialog} />
            <Button label="Yes" icon="pi pi-check" className="p-button-text" onClick={deleteSelectedStudents} />
        </>
    );

    return (
        <div className="grid crud-demo">
            <div className="col-12">
                <div className="card">
                    <Toast ref={toast} />

                    <Toolbar className="mb-4" left={leftToolbarTemplate} right={rightToolbarTemplate}></Toolbar>

                    <DataTable
                        ref={dt}
                        value={students}
                        selection={selectedStudents}
                        onSelectionChange={(e) => setSelectedStudents(e.value)}
                        dataKey="id"
                        paginator
                        rows={10}
                        rowsPerPageOptions={[5, 10, 25]}
                        className="datatable-responsive"
                        paginatorTemplate="FirstPageLink PrevPageLink PageLinks NextPageLink LastPageLink CurrentPageReport RowsPerPageDropdown"
                        currentPageReportTemplate="Menampilkan {first} sampai {last} dari total {totalRecords} siswa"
                        globalFilter={globalFilter}
                        emptyMessage="Siswa masih kosong."
                        header={header}
                    >
                        <Column selectionMode="multiple" headerStyle={{ width: "3rem" }}></Column>
                        <Column field="nisn" header="NISN" sortable body={nisnBodyTemplate}></Column>
                        <Column field="nis" header="NIS" sortable body={nisBodyTemplate}></Column>
                        <Column field="nama" header="Nama" sortable body={namaBodyTemplate}></Column>
                        <Column field="kotaLahir" header="Kota Lahir" sortable body={kotaLahirBodyTemplate}></Column>
                        <Column field="tanggalLahir" header="Tanggal Lahir" sortable body={tanggalLahirBodyTemplate}></Column>
                        <Column header="Aksi" body={actionBodyTemplate}></Column>
                    </DataTable>

                    <Dialog visible={studentDialog} style={{ width: "450px" }} header="Detail Siswa" modal className="p-fluid" footer={studentDialogFooter} onHide={hideStudent}>
                        <div className="field">
                            <label htmlFor="nisn">NISN</label>
                            <InputText id="nisn" value={student.nisn} onChange={(e) => onInputChange(e, "nisn")} type="number" required autoFocus className={classNames({ "p-invalid": submitted && !student.nisn })} />
                            {submitted && !student.nisn && <small className="p-invalid">NISN tidak boleh kosong.</small>}
                        </div>
                        <div className="field">
                            <label htmlFor="nis">NIS</label>
                            <InputText id="nis" value={student.nis} onChange={(e) => onInputChange(e, "nis")} type="number" required className={classNames({ "p-invalid": submitted && !student.nis })} />
                            {submitted && !student.nis && <small className="p-invalid">NIS tidak boleh kosong.</small>}
                        </div>
                        <div className="field">
                            <label htmlFor="nama">Nama</label>
                            <InputText id="nama" value={student.nama} onChange={(e) => onInputChange(e, "nama")} required className={classNames({ "p-invalid": submitted && !student.nama })} />
                            {submitted && !student.nama && <small className="p-invalid">Nama tidak boleh kosong.</small>}
                        </div>
                        <div className="field">
                            <label htmlFor="nama">Tempat dan tanggal lahir</label>
                            <div className="grid formgrid">
                                <div className="col-12 mb-2 lg:col-6 lg:mb-0">
                                    <InputText id="kotaLahir" value={student.kotaLahir} onChange={(e) => onInputChange(e, "kotaLahir")} required className={classNames({ "p-invalid": submitted && !student.kotaLahir })} />
                                    {submitted && !student.kotaLahir && <small className="p-invalid">Kota lahir tidak boleh kosong.</small>}
                                </div>
                                <div className="col-12 mb-2 lg:col-6 lg:mb-0">
                                    <Calendar
                                        showIcon
                                        showButtonBar
                                        value={student.tanggalLahir}
                                        dateFormat="dd/mm/yy"
                                        monthNavigator
                                        yearNavigator
                                        yearRange="1980:2030"
                                        placeholder={student.tanggalLahir && student.tanggalLahir}
                                        onChange={(e) => onInputChange(e, "tanggalLahir")}
                                        className={classNames({ "p-invalid": submitted && !student.tanggalLahir })}
                                    ></Calendar>
                                    {submitted && !student.tanggalLahir && <small className="p-invalid">Tanggal lahir tidak boleh kosong.</small>}
                                </div>
                            </div>
                        </div>
                        <div className="field">
                            <label htmlFor="nama">Tahun Masuk</label>
                            <Dropdown
                                id="tahunPelajaran"
                                autoFocus
                                value={{ tahunPelajaran: student.tahunPelajaran }}
                                onChange={(e) => onChangeTp(e.value)}
                                options={tahunPelajaranItem}
                                optionLabel="tahunPelajaran"
                                placeholder="Pilih satu"
                                className={classNames({ "p-invalid": submitted && !student.tahunPelajaran })}
                            ></Dropdown>
                            {submitted && !student.tahunPelajaran && <small className="p-invalid">Tahun pelajaran tidak boleh kosong.</small>}
                        </div>
                    </Dialog>

                    <Dialog visible={deleteStudentDialog} style={{ width: "450px" }} header="Confirm" modal footer={deleteStudentDialogFooter} onHide={hideDeleteStudentDialog}>
                        <div className="flex align-items-center justify-content-center">
                            <i className="pi pi-exclamation-triangle mr-3" style={{ fontSize: "2rem" }} />
                            {student && (
                                <span>
                                    Anda yakin akan menghapus <b>{student.nama}</b>?
                                </span>
                            )}
                        </div>
                    </Dialog>

                    <Dialog visible={deleteStudentsDialog} style={{ width: "450px" }} header="Confirm" modal footer={deleteStudentsDialogFooter} onHide={hideDeleteStudentsDialog}>
                        <div className="flex align-items-center justify-content-center">
                            <i className="pi pi-exclamation-triangle mr-3" style={{ fontSize: "2rem" }} />
                            {students && <span>Kamu yakin akan menghapus semua siswa yang ditandai?</span>}
                        </div>
                    </Dialog>
                    <Dialog visible={totalSize} header="Mohon Tunggu..." style={{ width: "450px" }} modal onHide={() => setTotalSize(false)}>
                        <ProgressBar mode="indeterminate" style={{ height: "6px" }}></ProgressBar>
                    </Dialog>
                </div>
            </div>
        </div>
    );
};
